// src/components/QuranProgressView.js
import React, { useState, useEffect, useCallback, useRef } from 'react';
import {
  Box,
  Typography,
  Paper,
  CircularProgress,
  Divider,
  Grid,
  Button,
  Chip,
  useTheme,
  alpha,
  Tooltip,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Collapse,
  Fade,
  ButtonBase
} from '@mui/material';
import CalendarTodayIcon from '@mui/icons-material/CalendarToday';
import StarIcon from '@mui/icons-material/Star';
import LoopIcon from '@mui/icons-material/Loop';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import RefreshIcon from '@mui/icons-material/Refresh';
import ExpandMoreIcon from '@mui/icons-material/ExpandLess';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import { motion, AnimatePresence } from 'framer-motion';
import quranProgressTracker from '../utils/QuranProgressTracker';
import quranService from '../services/quranService';

/**
 * Component for displaying Quran reading progress
 */
const QuranProgressView = ({ onNavigate }) => {
  const theme = useTheme();
  const [overallProgress, setOverallProgress] = useState({ completed: 0, total: 6236, percentage: 0 });
  const [lastRead, setLastRead] = useState(null);
  const [readingStreak, setReadingStreak] = useState(null);
  const [recentActivity, setRecentActivity] = useState([]);
  const [dailyProgress, setDailyProgress] = useState({ completed: 0, target: 20, percentage: 0 });
  const [surahName, setSurahName] = useState("");
  const [refreshKey, setRefreshKey] = useState(0);
  const [showResetDialog, setShowResetDialog] = useState(false);
  const [expanded, setExpanded] = useState(false);
  const progressRef = useRef(null);

  // Theme-aware colors
  const bgColor = theme.palette.background.paper;
  const borderColor = theme.palette.divider;
  const highlightColor = theme.palette.primary.main;

  const progressVariants = {
    hidden: { opacity: 0, y: 30 },
    visible: { opacity: 1, y: 0, transition: { duration: 0.6 } },
  };
  const circleVariants = {
    initial: { scale: 0.8, opacity: 0 },
    animate: { scale: 1, opacity: 1, transition: { type: 'spring', stiffness: 120 } },
  };
  const badgeVariants = {
    initial: { scale: 0, rotate: -30 },
    animate: { scale: 1, rotate: 0, transition: { type: 'spring', stiffness: 200 } },
  };

  // Fetch progress data function
  const fetchProgressData = useCallback(async () => {
    // Get overall progress
    const progress = quranProgressTracker.getOverallProgress();
    setOverallProgress(progress);

    // Get last read position
    const lastPosition = quranProgressTracker.getLastReadPosition();
    setLastRead(lastPosition);

    // Get reading streak
    const streak = quranProgressTracker.getReadingStreak() || {
      currentStreak: 0,
      longestStreak: 0,
      history: []
    };
    setReadingStreak(streak);

    // Get daily progress
    const daily = quranProgressTracker.getDailyProgress() || {
      completed: 0,
      target: 20,
      percentage: 0
    };
    setDailyProgress(daily);

    // Calculate recent activity from reading history if available
    if (streak?.history && streak.history.length > 0) {
      const lastDays = streak.history.slice(-7).reverse();
      setRecentActivity(lastDays);
    }

    // If we have a last read position, try to get the surah name
    if (lastPosition?.surah) {
      try {
        const quran = await quranService.loadEdition('quran-tajweed');
        const surah = quran.getSurah(lastPosition.surah);
        if (surah) {
          setSurahName(surah.englishName);
        }
      } catch (err) {
        console.error("Error loading quran data for surah name:", err);
      }
    }
  }, []);

  // Fetch progress data on component mount and when refresh is triggered
  useEffect(() => {
    fetchProgressData();

    // Set up event listener for storage changes (in case multiple tabs are open)
    const handleStorageChange = () => {
      fetchProgressData();
    };

    window.addEventListener('storage', handleStorageChange);
    document.addEventListener('quranProgressUpdated', handleStorageChange);

    return () => {
      window.removeEventListener('storage', handleStorageChange);
      document.removeEventListener('quranProgressUpdated', handleStorageChange);
    };
  }, [fetchProgressData, refreshKey]);

  // Persist expand/collapse state in localStorage
  useEffect(() => {
    const saved = localStorage.getItem('quranProgressExpanded');
    if (saved !== null) setExpanded(saved === 'true');
  }, []);
  useEffect(() => {
    localStorage.setItem('quranProgressExpanded', expanded);
  }, [expanded]);

  const handleResumeReading = () => {
    if (lastRead && typeof onNavigate === 'function') {
      onNavigate('surah', lastRead.surah, lastRead.ayah);
    }
  };

  const handleRefresh = () => {
    setRefreshKey(prev => prev + 1);
    fetchProgressData();
  };

  const handleResetProgress = () => {
    setShowResetDialog(true);
  };

  const confirmResetProgress = () => {
    quranProgressTracker.resetProgress();
    setShowResetDialog(false);
    handleRefresh();

    // Dispatch a custom event to notify other components
    document.dispatchEvent(new Event('quranProgressUpdated'));
  };

  const formatTimeSince = (timestamp) => {
    if (!timestamp) return '';

    const now = new Date();
    const readTime = new Date(timestamp);
    const diffMs = now - readTime;
    const diffMins = Math.floor(diffMs / (1000 * 60));
    const diffHours = Math.floor(diffMins / 60);
    const diffDays = Math.floor(diffHours / 24);

    if (diffDays > 0) return `${diffDays} day${diffDays > 1 ? 's' : ''} ago`;
    if (diffHours > 0) return `${diffHours} hour${diffHours > 1 ? 's' : ''} ago`;
    if (diffMins > 0) return `${diffMins} minute${diffMins > 1 ? 's' : ''} ago`;
    return 'just now';
  };

  const toggleExpand = () => {
    setExpanded(!expanded);
  };

  return (
    <motion.div
      variants={progressVariants}
      initial="hidden"
      animate="visible"
    >
      <Box
        component={Paper}
        elevation={2}
        sx={{
          borderRadius: 2,
          p: 3,
          mb: 3,
          border: `1px solid ${borderColor}`,
          boxShadow: theme.shadows[2],
          transition: 'all 0.3s ease',
          '&:hover': {
            boxShadow: theme.shadows[6],
          }
        }}
        ref={progressRef}
      >
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Typography variant="h5" component="h2" sx={{ fontWeight: 'bold' }}>
              Your Quran Reading Journey
            </Typography>
            <Tooltip title={expanded ? 'Collapse details' : 'Expand for more details'}>
              <IconButton onClick={toggleExpand} size="small" sx={{ ml: 1 }}>
                {expanded ? <ExpandLessIcon /> : <ExpandMoreIcon />}
              </IconButton>
            </Tooltip>
          </Box>
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Button
              variant="outlined"
              color="error"
              size="small"
              onClick={handleResetProgress}
              startIcon={<RefreshIcon />}
              sx={{ mr: 1 }}
            >
              Reset Progress
            </Button>
            <Tooltip title="Refresh progress data">
              <IconButton onClick={handleRefresh} size="small">
                <RefreshIcon />
              </IconButton>
            </Tooltip>
          </Box>
        </Box>

        <Collapse in={expanded} timeout={400}>
          {/* Overall progress section with recent activity */}
          <Box
            sx={{
              display: 'flex',
              flexDirection: { xs: 'column', md: 'row' },
              alignItems: 'flex-start',
              mt: 2,
              mb: 1
            }}
          >
            {/* Left Column: Progress circle */}
            <Box sx={{ flex: 1, textAlign: 'center', mb: { xs: 3, md: 0 } }}>
              <motion.div
                variants={circleVariants}
                initial="initial"
                animate="animate"
                key={overallProgress.percentage}
                style={{ display: 'inline-block' }}
              >
                <Box position="relative" display="inline-flex">
                  <CircularProgress
                    variant="determinate"
                    value={overallProgress.percentage || 0}
                    size={150}
                    thickness={5}
                    sx={{ color: highlightColor }}
                  />
                  <Box
                    top={0}
                    left={0}
                    bottom={0}
                    right={0}
                    position="absolute"
                    display="flex"
                    alignItems="center"
                    justifyContent="center"
                  >
                    <Typography variant="h4" fontWeight="bold">
                      {overallProgress.percentage || 0}%
                    </Typography>
                  </Box>
                </Box>
              </motion.div>
              <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                {overallProgress.completed || 0} of {overallProgress.total || 6236} ayahs read
              </Typography>
              {dailyProgress && (
                <Box sx={{ mt: 1, position: 'relative', display: 'inline-block' }}>
                  <Chip
                    label={`Today Goal: ${dailyProgress.completed}/${dailyProgress.target} ayahs`}
                    color={dailyProgress.completed >= dailyProgress.target ? 'success' : 'primary'}
                    variant="outlined"
                    size="small"
                  />
                  <AnimatePresence>
                    {dailyProgress.completed >= dailyProgress.target && (
                      <motion.div
                        variants={badgeVariants}
                        initial="initial"
                        animate="animate"
                        exit="initial"
                        style={{ position: 'absolute', top: -30, right: -30 }}
                      >
                        <Tooltip title="Daily goal achieved!">
                          <StarIcon color="warning" fontSize="large" />
                        </Tooltip>
                      </motion.div>
                    )}
                  </AnimatePresence>
                </Box>
              )}
              {/* Recent Activity */}
              <Box sx={{ mt: 2, textAlign: 'left' }}>
                <Typography variant="subtitle2" fontWeight="bold" color="text.secondary" sx={{ mb: 1 }}>
                  Recent Activity
                </Typography>
                {recentActivity && recentActivity.length > 0 ? (
                  <Grid container spacing={1} sx={{ mb: 1 }}>
                    {recentActivity.map((date, index) => (
                      <Grid item xs={1} key={index}>
                        <Tooltip title={new Date(date).toLocaleDateString()}>
                          <motion.div
                            whileHover={{ scale: 1.2 }}
                            style={{
                              height: 20,
                              background: index === 0 ? theme.palette.success.main : alpha(highlightColor, 1 - (index * 0.1)),
                              borderRadius: 4,
                              boxShadow: index === 0 ? `0 0 8px ${theme.palette.success.main}` : undefined,
                              border: index === 0 ? `2px solid ${theme.palette.success.light}` : undefined,
                              transition: 'all 0.2s',
                            }}
                          />
                        </Tooltip>
                      </Grid>
                    ))}
                  </Grid>
                ) : (
                  <Typography variant="body2" color="text.secondary" fontSize="0.8rem">
                    No recent reading activity
                  </Typography>
                )}
              </Box>
            </Box>
            {/* Right Column: Last read and streak info */}
            <Box sx={{ flex: 2, ml: { md: 4 } }}>
              {/* Last read section */}
              <Box sx={{ mb: 3 }}>
                <Typography variant="subtitle1" fontWeight="bold" color="text.secondary">
                  Last Read Position
                </Typography>
                {lastRead ? (
                  <>
                    <Typography variant="h6" fontWeight="medium">
                      Surah {lastRead.surah} ({surahName || ""}), Ayah {lastRead.ayah}
                    </Typography>
                    <Box sx={{ display: 'flex', alignItems: 'center', mt: 0.5 }}>
                      <CalendarTodayIcon sx={{ fontSize: 'small', mr: 0.5, color: 'text.secondary' }} />
                      <Typography variant="body2" color="text.secondary">
                        {formatTimeSince(lastRead.timestamp)}
                      </Typography>
                    </Box>
                    <motion.div
                      whileHover={{ scale: 1.08 }}
                      whileTap={{ scale: 0.98 }}
                      style={{ display: 'inline-block' }}
                    >
                      <Button
                        startIcon={<CheckCircleIcon />}
                        variant="contained"
                        color="primary"
                        size="medium"
                        onClick={handleResumeReading}
                        sx={{ mt: 1, fontWeight: 'bold', px: 3, py: 1.2, fontSize: '1rem', boxShadow: 2 }}
                      >
                        Resume Reading
                      </Button>
                    </motion.div>
                  </>
                ) : (
                  <Typography variant="body1">No reading activity yet</Typography>
                )}
              </Box>
              {/* Reading streak section */}
              {readingStreak && (
                <Box sx={{ mt: 2 }}>
                  <Typography variant="subtitle1" fontWeight="bold" color="text.secondary">
                    Reading Streak
                  </Typography>
                  <Box sx={{ display: 'flex', alignItems: 'center' }}>
                    <Typography variant="h6" fontWeight="medium" sx={{ mr: 1 }}>
                      {readingStreak.currentStreak || 0} day{(readingStreak.currentStreak || 0) !== 1 ? 's' : ''}
                    </Typography>
                    <Tooltip title={`Your longest streak is ${readingStreak.longestStreak || 0} days!`}>
                      <Chip
                        icon={<StarIcon />}
                        label="Current"
                        size="small"
                        color="warning"
                        sx={{ fontWeight: 'bold' }}
                      />
                    </Tooltip>
                    <AnimatePresence>
                      {readingStreak.currentStreak > 0 && readingStreak.currentStreak === readingStreak.longestStreak && (
                        <motion.div
                          variants={badgeVariants}
                          initial="initial"
                          animate="animate"
                          exit="initial"
                          style={{ marginLeft: 10 }}
                        >
                          <Tooltip title="Longest streak!">
                            <StarIcon color="success" fontSize="large" />
                          </Tooltip>
                        </motion.div>
                      )}
                    </AnimatePresence>
                  </Box>
                  <Box sx={{ display: 'flex', alignItems: 'center', mt: 0.5 }}>
                    <LoopIcon sx={{ fontSize: 'small', mr: 0.5, color: 'text.secondary' }} />
                    <Typography variant="body2" color="text.secondary">
                      Longest: {readingStreak.longestStreak || 0} day{(readingStreak.longestStreak || 0) !== 1 ? 's' : ''}
                    </Typography>
                  </Box>
                </Box>
              )}
            </Box>
          </Box>
        </Collapse>
        {/* When collapsed, show a summary */}
        <Fade in={!expanded}>
          <ButtonBase
            onClick={toggleExpand}
            sx={{
              width: '100%',
              textAlign: 'left',
              borderRadius: 2,
              display: expanded ? 'none' : 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
              mt: 1,
              p: 1,
              background: 'transparent',
              '&:hover': { background: alpha(highlightColor, 0.04) }
            }}
            focusRipple
          >
            <Typography variant="body2" color="text.secondary">
              {overallProgress.percentage || 0}% complete 
              {dailyProgress ? ` ${dailyProgress.completed}/${dailyProgress.target} today ` : ''}
              {readingStreak ? ` ${readingStreak.currentStreak} day streak` : ''}
            </Typography>
            <Typography variant="body2" color="primary">
              Click to expand
            </Typography>
          </ButtonBase>
        </Fade>
        {/* Reset Confirmation Dialog */}
        <Dialog open={showResetDialog} onClose={() => setShowResetDialog(false)}>
          <DialogTitle>Reset Progress?</DialogTitle>
          <DialogContent>
            <Typography>
              Are you sure you want to reset all your Quran reading progress? This action cannot be undone.
            </Typography>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setShowResetDialog(false)}>Cancel</Button>
            <Button onClick={confirmResetProgress} color="error" variant="contained">Reset All Progress</Button>
          </DialogActions>
        </Dialog>
      </Box>
    </motion.div>
  );
};

export default QuranProgressView;