class Word {
  constructor(data) {
    // Assign all properties from the data object, including audio
    Object.assign(this, data);
  }

  // Helper to generate audio_url if not provided
  static generateAudioUrl(surah, ayah, word) {
    // All numbers are zero-padded to 3 digits
    return `${String(surah).padStart(3, '0')}_${String(ayah).padStart(3, '0')}_${String(word).padStart(3, '0')}.mp3`;
  }
}

export default Word;