import Surah from './Surah';
import QuranEdition from './QuranEdition';

/**
 * Represents the full Quran with all its surahs and edition information
 */
class Quran {
  constructor(data) {
    this.surahs = data.surahs?.map(surah => new Surah(surah)) || [];
    this.edition = new QuranEdition(data.edition);
  }
  
  // Get surah by number
  getSurah(number) {
    return this.surahs.find(surah => surah.number === number);
  }
  
  // Get surah by name
  getSurahByName(name) {
    return this.surahs.find(surah => 
      surah.name === name || 
      surah.englishName.toLowerCase() === name.toLowerCase()
    );
  }
  
  // Get ayah by global ayah number
  getAyah(globalNumber) {
    for (const surah of this.surahs) {
      const ayah = surah.ayahs.find(a => a.number === globalNumber);
      if (ayah) return { surah, ayah };
    }
    return null;
  }
  
  // Whether this is a tajweed edition
  isTajweed() {
    return this.edition.isTajweed();
  }
}

export default Quran;