// src/components/quran/TajweedTooltip.js
import React, { useState, useEffect } from 'react';
import { 
  Box, 
  Typography, 
  Paper,
  useTheme
} from '@mui/material';

/**
 * Component that creates a tooltip to display tajweed rule information
 * This tooltip appears when users hover over tajweed-colored text in the Quran
 */
const TajweedTooltip = () => {
  const theme = useTheme();
  const [tooltipData, setTooltipData] = useState(null);
  const [tooltipPosition, setTooltipPosition] = useState({ x: 0, y: 0 });

  // Initialize event listeners when component mounts
  useEffect(() => {
    // Function to handle mouseenter on tajweed spans
    const handleMouseEnter = (e) => {
      // Check if the target has tajweed data attribute
      const target = e.target;
      if (target.hasAttribute('data-tajweed-rule')) {
        // Extract rule information
        const ruleData = target.getAttribute('data-tajweed-rule');
        const [title, description] = ruleData.split(':');
        const color = target.getAttribute('data-tajweed-color') || '#000000';
        
        // Calculate position for the tooltip
        const rect = target.getBoundingClientRect();
        const x = rect.left + window.scrollX + (rect.width / 2);
        const y = rect.top + window.scrollY - 10; // Position above the text
        
        // Update tooltip state
        setTooltipData({ title, description, color });
        setTooltipPosition({ x, y });
      }
    };

    // Function to handle mouseleave
    const handleMouseLeave = () => {
      setTooltipData(null);
    };

    // Add global event listener to document for better performance
    document.addEventListener('mouseover', (e) => {
      if (e.target.hasAttribute('data-tajweed-rule')) {
        handleMouseEnter(e);
      } else if (!e.target.closest('[data-tajweed-rule]')) {
        // Only hide if we're not still within a tajweed element
        handleMouseLeave();
      }
    });

    // Clean up
    return () => {
      document.removeEventListener('mouseover', handleMouseEnter);
    };
  }, []);

  // Don't render anything if no tooltip data
  if (!tooltipData) return null;

  return (
    <Paper
      elevation={4}
      sx={{
        position: 'absolute',
        left: `${tooltipPosition.x}px`,
        top: `${tooltipPosition.y}px`,
        transform: 'translate(-50%, -100%)',
        p: 1.5,
        maxWidth: '300px',
        zIndex: 1500,
        borderLeft: `4px solid ${tooltipData.color}`,
        animation: 'fadeIn 0.2s ease-in-out',
        '@keyframes fadeIn': {
          from: { opacity: 0, transform: 'translate(-50%, -90%)' },
          to: { opacity: 1, transform: 'translate(-50%, -100%)' }
        }
      }}
    >
      <Typography variant="subtitle2" sx={{ fontWeight: 600, color: tooltipData.color }}>
        {tooltipData.title}
      </Typography>
      <Typography variant="body2">
        {tooltipData.description}
      </Typography>
    </Paper>
  );
};

export default TajweedTooltip;