/**
 * QuranProgressTracker - A utility for tracking a user's progress through Quran reading
 * 
 * Features:
 * - Track which ayahs have been read
 * - Record last read position
 * - Calculate overall reading progress
 * - Track reading streaks
 * - Persist data in localStorage
 * - Support for setting reading goals
 */

// Constants
const STORAGE_KEYS = {
  READ_AYAHS: 'quran-progress-read-ayahs',
  LAST_POSITION: 'quran-progress-last-position',
  READING_STREAK: 'quran-progress-reading-streak',
  READING_GOALS: 'quran-progress-goals'
};

// Quran structure constants
const QURAN_STATS = {
  TOTAL_SURAHS: 114,
  TOTAL_AYAHS: 6236,
  TOTAL_JUZS: 30
};

class QuranProgressTracker {
  constructor() {
    // Initialize storage if needed
    this._initializeStorage();
  }

  /**
   * Initialize storage with default values if not already set
   * @private
   */
  _initializeStorage() {
    if (!localStorage.getItem(STORAGE_KEYS.READ_AYAHS)) {
      localStorage.setItem(STORAGE_KEYS.READ_AYAHS, JSON.stringify({}));
    }

    if (!localStorage.getItem(STORAGE_KEYS.READING_STREAK)) {
      const defaultStreak = {
        currentStreak: 0,
        longestStreak: 0,
        lastReadDate: null,
        history: []
      };
      localStorage.setItem(STORAGE_KEYS.READING_STREAK, JSON.stringify(defaultStreak));
    }

    if (!localStorage.getItem(STORAGE_KEYS.READING_GOALS)) {
      const defaultGoals = {
        dailyAyahs: 20,
        completionTarget: null // Target date to complete Quran
      };
      localStorage.setItem(STORAGE_KEYS.READING_GOALS, JSON.stringify(defaultGoals));
    }
  }

  /**
   * Mark an ayah as read
   * @param {number} surah - Surah number (1-114)
   * @param {number} ayah - Ayah number
   * @param {boolean} isComplete - Whether the ayah was fully read
   */
  markAsRead(surah, ayah, isComplete = true) {
    if (!this._isValidSurah(surah) || !this._isValidAyah(surah, ayah)) {
      console.error('Invalid surah or ayah number');
      return false;
    }

    try {
      // Update read ayahs record
      const readAyahs = JSON.parse(localStorage.getItem(STORAGE_KEYS.READ_AYAHS)) || {};
      
      if (!readAyahs[surah]) {
        readAyahs[surah] = {};
      }
      
      // Only mark if not already read
      const timestamp = new Date().toISOString();
      readAyahs[surah][ayah] = {
        read: isComplete,
        timestamp: timestamp
      };
      
      localStorage.setItem(STORAGE_KEYS.READ_AYAHS, JSON.stringify(readAyahs));
      
      // Update last read position
      this.updateLastReadPosition(surah, ayah);
      
      // Update reading streak
      this._updateReadingStreak();

      // Dispatch storage event to notify other components
      window.dispatchEvent(new Event('storage'));
      
      return true;
    } catch (error) {
      console.error('Error marking ayah as read:', error);
      return false;
    }
  }

  /**
   * Mark a range of ayahs as read
   * @param {number} startSurah - Starting surah number
   * @param {number} startAyah - Starting ayah number
   * @param {number} endSurah - Ending surah number
   * @param {number} endAyah - Ending ayah number
   */
  markRangeAsRead(startSurah, startAyah, endSurah, endAyah) {
    if (startSurah > endSurah || (startSurah === endSurah && startAyah > endAyah)) {
      // Swap start and end if they're in the wrong order
      [startSurah, startAyah, endSurah, endAyah] = [endSurah, endAyah, startSurah, startAyah];
    }

    try {
      const readAyahs = JSON.parse(localStorage.getItem(STORAGE_KEYS.READ_AYAHS)) || {};
      const timestamp = new Date().toISOString();
      
      // Mark each ayah in the range as read
      for (let s = startSurah; s <= endSurah; s++) {
        if (!readAyahs[s]) {
          readAyahs[s] = {};
        }
        
        // Calculate start and end ayah for this surah
        const firstAyah = (s === startSurah) ? startAyah : 1;
        const lastAyah = (s === endSurah) ? endAyah : this._getSurahAyahCount(s);
        
        for (let a = firstAyah; a <= lastAyah; a++) {
          readAyahs[s][a] = {
            read: true,
            timestamp
          };
        }
      }
      
      localStorage.setItem(STORAGE_KEYS.READ_AYAHS, JSON.stringify(readAyahs));
      
      // Update last read position to the end of the range
      this.updateLastReadPosition(endSurah, endAyah);
      
      // Update reading streak
      this._updateReadingStreak();
      
      return true;
    } catch (error) {
      console.error('Error marking ayah range as read:', error);
      return false;
    }
  }

  /**
   * Update the last read position
   * @param {number} surah - Surah number
   * @param {number} ayah - Ayah number
   */
  updateLastReadPosition(surah, ayah) {
    if (!this._isValidSurah(surah) || !this._isValidAyah(surah, ayah)) {
      console.error('Invalid surah or ayah number');
      return false;
    }

    try {
      const lastPosition = {
        surah,
        ayah,
        timestamp: new Date().toISOString()
      };
      
      localStorage.setItem(STORAGE_KEYS.LAST_POSITION, JSON.stringify(lastPosition));
      return true;
    } catch (error) {
      console.error('Error updating last read position:', error);
      return false;
    }
  }

  /**
   * Get the last read position
   * @returns {Object|null} Last read position {surah, ayah, timestamp}
   */
  getLastReadPosition() {
    try {
      const lastPosition = localStorage.getItem(STORAGE_KEYS.LAST_POSITION);
      return lastPosition ? JSON.parse(lastPosition) : null;
    } catch (error) {
      console.error('Error getting last read position:', error);
      return null;
    }
  }

  /**
   * Check if an ayah has been read
   * @param {number} surah - Surah number
   * @param {number} ayah - Ayah number
   * @returns {boolean} Whether the ayah has been read
   */
  isAyahRead(surah, ayah) {
    try {
      const readAyahs = JSON.parse(localStorage.getItem(STORAGE_KEYS.READ_AYAHS)) || {};
      return !!(readAyahs[surah] && readAyahs[surah][ayah] && readAyahs[surah][ayah].read);
    } catch (error) {
      console.error('Error checking if ayah is read:', error);
      return false;
    }
  }

  /**
   * Get the timestamp of when an ayah was read
   * @param {number} surah - Surah number
   * @param {number} ayah - Ayah number
   * @returns {string|null} ISO timestamp string or null if not read
   */
  getAyahReadTimestamp(surah, ayah) {
    try {
      const readAyahs = JSON.parse(localStorage.getItem(STORAGE_KEYS.READ_AYAHS)) || {};
      
      if (readAyahs[surah] && readAyahs[surah][ayah] && readAyahs[surah][ayah].read) {
        return readAyahs[surah][ayah].timestamp;
      }
      
      return null;
    } catch (error) {
      console.error('Error getting ayah read timestamp:', error);
      return null;
    }
  }

  /**
   * Calculate and return the overall reading progress
   * @returns {Object} Progress object with completed count, total, and percentage
   */
  getOverallProgress() {
    try {
      const readAyahs = JSON.parse(localStorage.getItem(STORAGE_KEYS.READ_AYAHS)) || {};
      let completedCount = 0;
      
      // Count all read ayahs
      Object.keys(readAyahs).forEach(surah => {
        Object.keys(readAyahs[surah]).forEach(ayah => {
          if (readAyahs[surah][ayah].read) {
            completedCount++;
          }
        });
      });
      
      const percentage = Math.round((completedCount / QURAN_STATS.TOTAL_AYAHS) * 100);
      
      return {
        completed: completedCount,
        total: QURAN_STATS.TOTAL_AYAHS,
        percentage
      };
    } catch (error) {
      console.error('Error calculating overall progress:', error);
      return { completed: 0, total: QURAN_STATS.TOTAL_AYAHS, percentage: 0 };
    }
  }

  /**
   * Get surah-level progress
   * @param {number} surah - Surah number
   * @returns {Object} Progress object with completed count, total, and percentage
   */
  getSurahProgress(surah) {
    if (!this._isValidSurah(surah)) {
      console.error('Invalid surah number');
      return { completed: 0, total: 0, percentage: 0 };
    }

    try {
      const readAyahs = JSON.parse(localStorage.getItem(STORAGE_KEYS.READ_AYAHS)) || {};
      const surahAyahCount = this._getSurahAyahCount(surah);
      let completedCount = 0;
      
      if (readAyahs[surah]) {
        Object.keys(readAyahs[surah]).forEach(ayah => {
          if (readAyahs[surah][ayah].read) {
            completedCount++;
          }
        });
      }
      
      const percentage = surahAyahCount > 0 ? 
        Math.round((completedCount / surahAyahCount) * 100) : 0;
      
      return {
        completed: completedCount,
        total: surahAyahCount,
        percentage
      };
    } catch (error) {
      console.error('Error calculating surah progress:', error);
      return { completed: 0, total: this._getSurahAyahCount(surah), percentage: 0 };
    }
  }

  /**
   * Get juz-level progress
   * @param {number} juz - Juz number (1-30)
   * @returns {Object} Progress object with completed count, total, and percentage
   */
  getJuzProgress(juz) {
    if (juz < 1 || juz > 30) {
      console.error('Invalid juz number');
      return { completed: 0, total: 0, percentage: 0 };
    }

    // This would require a mapping of juz boundaries
    // For now, returning a placeholder
    return { completed: 0, total: 0, percentage: 0 };
  }

  /**
   * Reset progress for the entire Quran or a specific surah
   * @param {number|null} surah - Surah number or null to reset all
   */
  resetProgress(surah = null) {
    try {
      if (surah !== null) {
        if (!this._isValidSurah(surah)) {
          console.error('Invalid surah number');
          return false;
        }
        
        const readAyahs = JSON.parse(localStorage.getItem(STORAGE_KEYS.READ_AYAHS)) || {};
        delete readAyahs[surah];
        localStorage.setItem(STORAGE_KEYS.READ_AYAHS, JSON.stringify(readAyahs));
      } else {
        // Reset all progress
        localStorage.setItem(STORAGE_KEYS.READ_AYAHS, JSON.stringify({}));
        localStorage.removeItem(STORAGE_KEYS.LAST_POSITION);
        
        // Reset reading streak
        const streakData = {
          currentStreak: 0,
          longestStreak: 0,
          lastReadDate: null,
          history: []
        };
        localStorage.setItem(STORAGE_KEYS.READING_STREAK, JSON.stringify(streakData));
        
        // Keep reading goals as they are
        
        // Notify other components that storage has changed
        window.dispatchEvent(new Event('storage'));
      }
      
      return true;
    } catch (error) {
      console.error('Error resetting progress:', error);
      return false;
    }
  }

  /**
   * Get reading streak information
   * @returns {Object} Streak information
   */
  getReadingStreak() {
    try {
      const streak = localStorage.getItem(STORAGE_KEYS.READING_STREAK);
      return streak ? JSON.parse(streak) : null;
    } catch (error) {
      console.error('Error getting reading streak:', error);
      return null;
    }
  }

  /**
   * Set reading goals
   * @param {Object} goals - The goals object
   * @param {number} goals.dailyAyahs - Target number of ayahs to read daily
   * @param {string|null} goals.completionTarget - Target date for completing the Quran
   */
  setReadingGoals(goals) {
    try {
      const currentGoals = JSON.parse(localStorage.getItem(STORAGE_KEYS.READING_GOALS)) || {};
      const updatedGoals = { ...currentGoals, ...goals };
      localStorage.setItem(STORAGE_KEYS.READING_GOALS, JSON.stringify(updatedGoals));
      return true;
    } catch (error) {
      console.error('Error setting reading goals:', error);
      return false;
    }
  }

  /**
   * Get current reading goals
   * @returns {Object} Reading goals
   */
  getReadingGoals() {
    try {
      const goals = localStorage.getItem(STORAGE_KEYS.READING_GOALS);
      return goals ? JSON.parse(goals) : null;
    } catch (error) {
      console.error('Error getting reading goals:', error);
      return null;
    }
  }

  /**
   * Calculate daily progress toward goals
   * @returns {Object} Daily progress information
   */
  getDailyProgress() {
    try {
      const goals = this.getReadingGoals();
      if (!goals || !goals.dailyAyahs) return null;
      
      const today = new Date().toISOString().split('T')[0]; // YYYY-MM-DD
      const readAyahs = JSON.parse(localStorage.getItem(STORAGE_KEYS.READ_AYAHS)) || {};
      let todayCount = 0;
      
      // Count ayahs read today
      Object.keys(readAyahs).forEach(surah => {
        Object.keys(readAyahs[surah]).forEach(ayah => {
          const timestamp = readAyahs[surah][ayah].timestamp;
          if (timestamp && timestamp.startsWith(today)) {
            todayCount++;
          }
        });
      });
      
      const percentage = Math.min(100, Math.round((todayCount / goals.dailyAyahs) * 100));
      
      return {
        completed: todayCount,
        target: goals.dailyAyahs,
        percentage,
        remainingToday: Math.max(0, goals.dailyAyahs - todayCount)
      };
    } catch (error) {
      console.error('Error calculating daily progress:', error);
      return null;
    }
  }

  /**
   * Export progress data (for backup or syncing)
   * @returns {Object} Full progress data
   */
  exportProgressData() {
    try {
      return {
        readAyahs: JSON.parse(localStorage.getItem(STORAGE_KEYS.READ_AYAHS) || '{}'),
        lastPosition: JSON.parse(localStorage.getItem(STORAGE_KEYS.LAST_POSITION) || 'null'),
        readingStreak: JSON.parse(localStorage.getItem(STORAGE_KEYS.READING_STREAK) || '{}'),
        goals: JSON.parse(localStorage.getItem(STORAGE_KEYS.READING_GOALS) || '{}')
      };
    } catch (error) {
      console.error('Error exporting progress data:', error);
      return null;
    }
  }

  /**
   * Import progress data (from backup or sync)
   * @param {Object} data - Progress data to import
   */
  importProgressData(data) {
    try {
      if (data.readAyahs) {
        localStorage.setItem(STORAGE_KEYS.READ_AYAHS, JSON.stringify(data.readAyahs));
      }
      if (data.lastPosition) {
        localStorage.setItem(STORAGE_KEYS.LAST_POSITION, JSON.stringify(data.lastPosition));
      }
      if (data.readingStreak) {
        localStorage.setItem(STORAGE_KEYS.READING_STREAK, JSON.stringify(data.readingStreak));
      }
      if (data.goals) {
        localStorage.setItem(STORAGE_KEYS.READING_GOALS, JSON.stringify(data.goals));
      }
      return true;
    } catch (error) {
      console.error('Error importing progress data:', error);
      return false;
    }
  }

  /**
   * Update reading streak based on current activity
   * @private
   */
  _updateReadingStreak() {
    try {
      const streak = JSON.parse(localStorage.getItem(STORAGE_KEYS.READING_STREAK)) || {
        currentStreak: 0,
        longestStreak: 0,
        lastReadDate: null,
        history: []
      };
      
      const today = new Date().toISOString().split('T')[0]; // YYYY-MM-DD
      
      if (streak.lastReadDate === today) {
        // Already counted for today, nothing to do
        return;
      }
      
      if (streak.lastReadDate) {
        const lastDate = new Date(streak.lastReadDate);
        const yesterday = new Date();
        yesterday.setDate(yesterday.getDate() - 1);
        yesterday.setHours(0, 0, 0, 0);
        const yesterdayString = yesterday.toISOString().split('T')[0];
        
        if (streak.lastReadDate === yesterdayString) {
          // Continuation of the streak - yesterday was the last read
          streak.currentStreak++;
        } else {
          // Break in streak
          streak.currentStreak = 1;
        }
      } else {
        // First reading
        streak.currentStreak = 1;
      }
      
      // Update longest streak if needed
      streak.longestStreak = Math.max(streak.longestStreak, streak.currentStreak);
      
      // Update the last read date
      streak.lastReadDate = today;
      
      // Add to history if not already there
      if (!streak.history.includes(today)) {
        streak.history.push(today);
        // Keep only the last 365 entries
        if (streak.history.length > 365) {
          streak.history.shift();
        }
      }
      
      localStorage.setItem(STORAGE_KEYS.READING_STREAK, JSON.stringify(streak));
    } catch (error) {
      console.error('Error updating reading streak:', error);
    }
  }

  /**
   * Check if a surah number is valid
   * @param {number} surah - Surah number to check
   * @returns {boolean} Whether the surah number is valid
   * @private
   */
  _isValidSurah(surah) {
    return surah >= 1 && surah <= QURAN_STATS.TOTAL_SURAHS;
  }

  /**
   * Check if an ayah number is valid for a given surah
   * @param {number} surah - Surah number
   * @param {number} ayah - Ayah number to check
   * @returns {boolean} Whether the ayah number is valid for the surah
   * @private
   */
  _isValidAyah(surah, ayah) {
    const ayahCount = this._getSurahAyahCount(surah);
    return ayah >= 1 && ayah <= ayahCount;
  }

  /**
   * Get the number of ayahs in a surah
   * @param {number} surah - Surah number
   * @returns {number} Number of ayahs in the surah
   * @private
   */
  _getSurahAyahCount(surah) {
    // This is a simplified version - in a real app, you'd have a complete mapping
    const ayahCounts = [
      7, 286, 200, 176, 120, 165, 206, 75, 129, 109, // 1-10
      123, 111, 43, 52, 99, 128, 111, 110, 98, 135,  // 11-20
      112, 78, 118, 64, 77, 227, 93, 88, 69, 60,     // 21-30
      34, 30, 73, 54, 45, 83, 182, 88, 75, 85,       // 31-40
      54, 53, 89, 59, 37, 35, 38, 29, 18, 45,        // 41-50
      60, 49, 62, 55, 78, 96, 29, 22, 24, 13,        // 51-60
      14, 11, 11, 18, 12, 12, 30, 52, 52, 44,        // 61-70
      28, 28, 20, 56, 40, 31, 50, 40, 46, 42,        // 71-80
      29, 19, 36, 25, 22, 17, 19, 26, 30, 20,        // 81-90
      15, 21, 11, 8, 8, 19, 5, 8, 8, 11,             // 91-100
      11, 8, 3, 9, 5, 4, 7, 3, 6, 3,                 // 101-110
      5, 4, 5, 6                                     // 111-114
    ];
    
    return ayahCounts[surah - 1] || 0;
  }
}

// Create a singleton instance
const quranProgressTracker = new QuranProgressTracker();
Object.freeze(quranProgressTracker);

export default quranProgressTracker;