// Custom Option for react-select
const ReciterOption = (props) => (
  <components.Option {...props}>
    <Box sx={{ display: 'flex', alignItems: 'center', p: 1 }}>
      <img
        src={props.data.image}
        alt={props.data.label}
        style={{ width: 48, height: 48, borderRadius: '50%', marginRight: 16, boxShadow: '0 2px 8px rgba(0,0,0,0.10)' }}
      />
      <Typography variant="body1" sx={{ fontWeight: 500 }}>{props.data.label}</Typography>
    </Box>
  </components.Option>
);

const ReciterSingleValue = (props) => (
  <components.SingleValue {...props}>
    <Box sx={{ display: 'flex', alignItems: 'center' }}>
      <img
        src={props.data.image}
        alt={props.data.label}
        style={{ width: 32, height: 32, borderRadius: '50%', marginRight: 10 }}
      />
      <Typography variant="body2" sx={{ fontWeight: 500 }}>{props.data.label}</Typography>
    </Box>
  </components.SingleValue>
);

const QuranSettingsPanel = ({ open, onClose }) => {
  const { settings, updateSetting, resetSettings, increaseFontSize, decreaseFontSize, quranEditions } = useQuranSettings();
  const [tabIndex, setTabIndex] = React.useState(0);
  const drawerTitleId = 'quran-settings-title'; // ID for Drawer title

  // Get available editions by type from quranEditions
  const quranTypes = quranEditions.filter(e => e.type === 'quran');
  const translations = quranEditions.filter(e => e.type === 'translation');
  const tafsirs = quranEditions.filter(e => e.type === 'tafsir');

  // Helper to group translations by language for react-select
  const groupedTranslations = Object.values(
    translations.reduce((acc, t) => {
      if (!acc[t.language]) acc[t.language] = { label: t.language, options: [] };
      acc[t.language].options.push({ value: t.identifier, label: t.name });
      return acc;
    }, {})
  );

  const handleTabChange = (event, newValue) => {
    setTabIndex(newValue);
  };

  const handleFontSizeSliderChange = (event, newValue) => {
    updateSetting('fontSize', newValue);
  };
  
  return (
    <Drawer
      anchor="right"
      open={open}
      onClose={onClose}
      aria-labelledby={drawerTitleId} // Label the drawer
    >
      <Box sx={{ width: { xs: '100vw', sm: 400 }, maxWidth: '100vw', p: 3 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
          <Typography variant="h6" id={drawerTitleId}>Quran Settings</Typography> {/* Add ID to title */}
          <IconButton onClick={onClose} edge="end" aria-label="Close settings panel"> {/* Add aria-label */}
            <CloseIcon />
          </IconButton>
        </Box>
        
        <Divider sx={{ mb: 3 }} />
        
        <Tabs
          value={tabIndex}
          onChange={handleTabChange}
          variant="fullWidth"
          sx={{ mb: 3 }}
          aria-label="Settings categories" // Label for the tab list
        >
          <Tab 
            icon={<TextFormatIcon />} 
            label="Font" 
            id="settings-tab-font" 
            aria-controls="settings-tabpanel-font" 
          />
          <Tab 
            icon={<TranslateIcon />} 
            label="Translation" 
            id="settings-tab-translation" 
            aria-controls="settings-tabpanel-translation" 
          />
          <Tab 
            icon={<MusicNoteIcon />} 
            label="Audio" 
            id="settings-tab-audio" 
            aria-controls="settings-tabpanel-audio" 
          />
          <Tab 
            icon={<VisibilityIcon />} 
            label="Display" 
            id="settings-tab-display" 
            aria-controls="settings-tabpanel-display" 
          />
        </Tabs>
        
        {/* Font Tab Panel */}
        <Box 
          role="tabpanel" 
          hidden={tabIndex !== 0} 
          id="settings-tabpanel-font" 
          aria-labelledby="settings-tab-font"
        >
          {tabIndex === 0 && (
            <Box>
              <Typography variant="subtitle2" gutterBottom id="arabic-font-label">
                Arabic Font
              </Typography>
              <FormControl fullWidth margin="normal">
                <InputLabel id="quran-edition-label">Quran Edition</InputLabel>
                <Select
                  labelId="quran-edition-label"
                  value={settings.quranEdition}
                  onChange={e => updateSetting('quranEdition', e.target.value)}
                  label="Quran Edition"
                >
                  {quranTypes.map(edition => (
                    <MenuItem key={edition.identifier} value={edition.identifier}>
                      {edition.name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>

              <Box sx={{ mb: 3 }} role="radiogroup" aria-labelledby="arabic-font-label"> {/* Group font selection */} 
                <Grid container spacing={1}>
                  {AVAILABLE_FONTS.map((font) => (
                    <Grid item xs={12} sm={6} key={font.name}>
                      <Paper
                        role="radio" // Treat as radio button
                        aria-checked={settings.fontFamily === font.family} // Indicate selection
                        tabIndex={settings.fontFamily === font.family ? 0 : -1} // Manage focus
                        onClick={() => updateSetting('fontFamily', font.family)}
                        onKeyPress={(e) => (e.key === 'Enter' || e.key === ' ') && updateSetting('fontFamily', font.family)} // Keyboard activation
                        sx={{
                          p: 2,
                          textAlign: 'center',
                          cursor: 'pointer',
                          border: settings.fontFamily === font.family ? '2px solid' : '1px solid',
                          borderColor: settings.fontFamily === font.family ? 'primary.main' : 'divider',
                          transition: 'all 0.2s',
                          '&:hover': {
                            borderColor: 'primary.main',
                            boxShadow: 1
                          }
                        }}
                      >
                        <Typography 
                          variant="body2" 
                          color="textSecondary" 
                          gutterBottom
                        >
                          {font.name}
                        </Typography>
                        <Typography 
                          sx={{ 
                            fontFamily: font.family,
                            fontSize: '1.2rem',
                            direction: 'rtl'
                          }}
                        >
                          {SAMPLE_TEXT}
                        </Typography>
                      </Paper>
                    </Grid>
                  ))}
                </Grid>
              </Box>
              
              <Typography variant="subtitle2" sx={{ mt: 3, mb: 1 }} id="font-size-label">
                Font Size: {settings.fontSize}px
              </Typography>
              
              <Box sx={{ 
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                mb: 2
              }}>
                <IconButton 
                  onClick={decreaseFontSize}
                  disabled={settings.fontSize <= MIN_FONT_SIZE}
                  color="primary"
                  aria-label="Decrease font size" // Add aria-label
                >
                  <RemoveIcon />
                </IconButton>
                
                <Slider
                  value={settings.fontSize}
                  onChange={handleFontSizeSliderChange}
                  aria-labelledby="font-size-label" // Use label ID
                  valueLabelDisplay="auto"
                  step={FONT_SIZE_STEP}
                  marks
                  min={MIN_FONT_SIZE}
                  max={MAX_FONT_SIZE}
                  sx={{ mx: 2 }}
                />
                
                <IconButton 
                  onClick={increaseFontSize}
                  disabled={settings.fontSize >= MAX_FONT_SIZE}
                  color="primary"
                  aria-label="Increase font size" // Add aria-label
                >
                  <AddIcon />
                </IconButton>
              </Box>
              
              <Box sx={{ 
                p: 2, 
                my: 2, 
                bgcolor: 'background.paper',
                borderRadius: 1,
                fontFamily: settings.fontFamily,
                fontSize: settings.fontSize,
                direction: 'rtl',
                textAlign: 'center'
              }}>
                {SAMPLE_TEXT}
              </Box>
            </Box>
          )}
        </Box>
        
        {/* Translation Tab Panel */}
        <Box 
          role="tabpanel" 
          hidden={tabIndex !== 1} 
          id="settings-tabpanel-translation" 
          aria-labelledby="settings-tab-translation"
        >
          {tabIndex === 1 && (
            <Box>
              <Typography variant="subtitle2" sx={{ mb: 1 }} id="translation-options-label">
                Translation Options
              </Typography>
              
              <FormControlLabel
                control={
                  <Switch
                    checked={settings.showTranslation}
                    onChange={(e) => updateSetting('showTranslation', e.target.checked)}
                    aria-labelledby="show-translation-label" // Link to label text
                  />
                }
                label="Show Translation"
                id="show-translation-label"
              />
              
              <FormControlLabel
                control={
                  <Switch
                    checked={settings.showTransliteration}
                    onChange={(e) => updateSetting('showTransliteration', e.target.checked)}
                    disabled={!settings.showTranslation}
                    aria-labelledby="show-transliteration-label" // Link to label text
                  />
                }
                label="Show Transliteration"
                id="show-transliteration-label"
              />
              
              {settings.showTranslation && (
                <Box sx={{ my: 2 }}>
                  <ReactSelect
                    inputId="translation-select"
                    classNamePrefix="react-select"
                    options={groupedTranslations}
                    value={(() => {
                      const found = translations.find(t => t.identifier === settings.translationId);
                      return found ? { value: found.identifier, label: found.name } : null;
                    })()}
                    onChange={option => updateSetting('translationId', option ? option.value : null)}
                    placeholder="Select translation..."
                    isClearable
                  />
                </Box>
              )}
              
              <Divider sx={{ my: 2 }} />
              
              <Typography variant="subtitle2" sx={{ mb: 1 }} id="tafsir-options-label">
                Tafsir Options
              </Typography>
              
              <FormControlLabel
                control={
                  <Switch
                    checked={settings.showTafsir}
                    onChange={(e) => updateSetting('showTafsir', e.target.checked)}
                    aria-labelledby="show-tafsir-label" // Link to label text
                  />
                }
                label="Show Tafsir"
                id="show-tafsir-label"
              />
              
              {settings.showTafsir && (
                <FormControl fullWidth margin="normal">
                  <InputLabel id="tafsir-label">Tafsir</InputLabel>
                  <Select
                    labelId="tafsir-label"
                    value={settings.tafsirId}
                    onChange={(e) => updateSetting('tafsirId', e.target.value)}
                    label="Tafsir"
                  >
                    {tafsirs.map((tafsir) => (
                      <MenuItem key={tafsir.identifier} value={tafsir.identifier}>
                        {tafsir.name} ({tafsir.language})
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              )}
              
              <FormControlLabel
                control={
                  <Switch
                    checked={settings.wordByWord}
                    onChange={(e) => updateSetting('wordByWord', e.target.checked)}
                    aria-labelledby="word-by-word-label" // Link to label text
                  />
                }
                label="Word-by-Word Translation"
                id="word-by-word-label"
                sx={{ mt: 1 }}
              />
            </Box>
          )}
        </Box>
        
        {/* Audio Tab Panel */}
        <Box 
          role="tabpanel" 
          hidden={tabIndex !== 2} 
          id="settings-tabpanel-audio" 
          aria-labelledby="settings-tab-audio"
        >
          {tabIndex === 2 && (
            <Box>
              <Typography variant="subtitle2" sx={{ mb: 1 }} id="audio-settings-label">
                Audio Settings
              </Typography>
              <Box sx={{ my: 2 }}>
                <ReactSelect
                  inputId="reciter-select"
                  classNamePrefix="react-select"
                  options={reciters}
                  components={{ Option: ReciterOption, SingleValue: ReciterSingleValue }}
                  value={reciters.find(r => r.value === settings.reciterId) || null}
                  onChange={option => updateSetting('reciterId', option ? option.value : null)}
                  placeholder="Select reciter..."
                  isClearable
                  styles={{
                    control: (base, state) => ({
                      ...base,
                      borderRadius: 18,
                      boxShadow: state.isFocused ? '0 2px 16px rgba(0,0,0,0.10)' : '0 2px 8px rgba(0,0,0,0.05)',
                      borderColor: state.isFocused ? '#1976d2' : base.borderColor,
                      minHeight: 56,
                    }),
                    option: (base, state) => ({
                      ...base,
                      background: state.isSelected ? '#e3f2fd' : state.isFocused ? '#f5faff' : 'white',
                      color: '#222',
                      borderRadius: 12,
                      margin: '2px 4px',
                      boxShadow: state.isFocused ? '0 2px 8px rgba(0,0,0,0.07)' : undefined,
                    }),
                  }}
                />
              </Box>
              <FormControlLabel
                control={
                  <Switch
                    checked={settings.autoPlayAyahs}
                    onChange={(e) => updateSetting('autoPlayAyahs', e.target.checked)}
                    aria-labelledby="autoplay-label" // Link to label text
                  />
                }
                label="Auto-play Next Ayah"
                id="autoplay-label"
                sx={{ mt: 1 }}
              />
            </Box>
          )}
        </Box>

        {/* Display Tab Panel */}
        <Box 
          role="tabpanel" 
          hidden={tabIndex !== 3} 
          id="settings-tabpanel-display" 
          aria-labelledby="settings-tab-display"
        >
          {tabIndex === 3 && (
            <Box>
              <Typography variant="subtitle2" sx={{ mb: 1 }} id="display-options-label">
                Display Options
              </Typography>
              
              <FormControlLabel
                control={
                  <Switch
                    checked={settings.showTajweed}
                    onChange={(e) => updateSetting('showTajweed', e.target.checked)}
                    aria-labelledby="show-tajweed-label" // Link to label text
                  />
                }
                label="Show Tajweed Colors"
                id="show-tajweed-label"
              />
              
              <FormControlLabel
                control={
                  <Switch
                    checked={settings.showPageView}
                    onChange={(e) => updateSetting('showPageView', e.target.checked)}
                    aria-labelledby="show-page-view-label" // Link to label text
                  />
                }
                label="Show in Page View"
                id="show-page-view-label"
              />
              
              <Typography variant="subtitle2" sx={{ mt: 2, mb: 1 }} id="navigation-mode-label">
                Navigation Mode
              </Typography>
              
              <RadioGroup
                aria-labelledby="navigation-mode-label" // Use label ID
                value={settings.navigationMode}
                onChange={(e) => updateSetting('navigationMode', e.target.value)}
              >
                <FormControlLabel value="page" control={<Radio />} label="Page" />
                <FormControlLabel value="surah" control={<Radio />} label="Surah" />
                <FormControlLabel value="juz" control={<Radio />} label="Juz" />
                <FormControlLabel value="hizb" control={<Radio />} label="Hizb" />
                <FormControlLabel value="ruku" control={<Radio />} label="Ruku" />
                <FormControlLabel value="manzil" control={<Radio />} label="Manzil" />
              </RadioGroup>
            </Box>
          )}
        </Box>
        
        <Box sx={{ mt: 3, textAlign: 'center' }}>
          <Button 
            variant="outlined" 
            color="secondary" 
            startIcon={<RestartAltIcon />}
            onClick={resetSettings}
          >
            Reset All Settings
          </Button>
        </Box>
      </Box>
    </Drawer>
  );
};

export default QuranSettingsPanel;