/**
 * Represents a single Ayah (verse) of the Quran
 */
import Word from './Word';

class Ayah {
  constructor(data) {
    this.number = data.number;
    this.text = data.text;
    this.numberInSurah = data.numberInSurah;
    this.juz = data.juz;
    this.manzil = data.manzil;
    this.page = data.page;
    this.ruku = data.ruku;
    this.hizbQuarter = data.hizbQuarter;
    this.sajda = data.sajda || false;
    
    // For translations and tafsir
    this.translation = data.translation || null;
    this.tafsir = data.tafsir || null;
    
    // For audio
    this.audioUrl = data.audioUrl || null;

    // For word-by-word support: parse words array if present in text (for quran-wordbyword-2)
    this.words = [];
    if (typeof data.text === 'string' && data.text.trim().startsWith('[')) {
      try {
        const wordsArray = JSON.parse(data.text);
        if (Array.isArray(wordsArray)) {
          this.words = wordsArray.map(wordObj => new Word(wordObj));
        }
      } catch (e) {
        // If parsing fails, fallback to empty array
        this.words = [];
      }
    }
  }
}

export default Ayah;