import React, { useEffect, useRef } from 'react';

console.log('WordByWordSpeechBubble module loaded');

const WordByWordSpeechBubble = ({ ayah, surah, selectedWord, setSelectedWord, settings, tajweedQuranData }) => {
  // Always call hooks at the top level
  const bubbleRef = useRef();
  useEffect(() => {
    function handleClick(event) {
      if (bubbleRef.current && !bubbleRef.current.contains(event.target)) {
        setSelectedWord && setSelectedWord(null);
      }
    }
    if (selectedWord) {
      document.addEventListener('mousedown', handleClick);
    }
    return () => {
      document.removeEventListener('mousedown', handleClick);
    };
  }, [selectedWord, setSelectedWord]);

  // Debug: always log props
  console.log('WordByWordSpeechBubble MOUNTED', {ayah, surah, settings});

  // Robustly handle ayah.text for quran-kids at the top
  let ayahText = ayah && ayah.text;
  console.log('WordByWordSpeechBubble typeof ayah.text:', typeof ayahText, ayahText);
  if (typeof ayahText !== 'string' && ayahText != null) {
    try {
      ayahText = JSON.parse(ayahText);
      console.log('Parsed ayahText:', ayahText);
    } catch (e) {
      ayahText = String(ayahText);
      console.log('Stringified ayahText:', ayahText);
    }
  }
  console.log('Final ayahText for rendering:', ayahText);

  if (!ayah) {
    console.log('WordByWordSpeechBubble: ayah is undefined or null');
    return <span style={{color: 'red'}}>No ayah data</span>;
  }

  return (
    <div
      style={{
        direction: 'rtl',
        textAlign: 'right',
        fontFamily: settings.fontFamily,
        fontSize: settings.fontSize,
        fontWeight: settings.fontWeight || 'bold',
        lineHeight: 2,
        marginBottom: 16,
        wordBreak: 'keep-all',
        textRendering: 'optimizeLegibility',
        WebkitFontSmoothing: 'antialiased',
      }}
    >
      {ayah.words && settings.quranEdition === 'quran-wordbyword-2' ? (
        ayah.words.map((word, idx) => {
          const isSelected = selectedWord &&
            selectedWord.surahNumber === surah.number &&
            selectedWord.ayahNumber === ayah.numberInSurah &&
            selectedWord.wordNumberInAyah === word.word_number_in_ayah;
          const arabicWithTajweed = word.word_arabic_tajweed || word.word_arabic;
          return (
            <span
              key={word.word_number_in_ayah}
              style={{
                cursor: 'pointer',
                color: isSelected ? '#fff' : '#1976d2',
                background: isSelected ? 'linear-gradient(90deg,#1976d2 60%,#42a5f5 100%)' : 'transparent',
                borderRadius: '8px',
                padding: '0 8px',
                marginLeft: 6,
                fontWeight: 'bold',
                transition: 'background 0.2s, color 0.2s',
                display: 'inline-block',
                position: 'relative',
                boxShadow: isSelected ? '0 2px 12px 0 rgba(33,150,243,0.15)' : 'none',
              }}
              title={word.word_translation || ''}
              onClick={e => {
                e.stopPropagation();
                setSelectedWord({
                  surahNumber: surah.number,
                  ayahNumber: ayah.numberInSurah,
                  wordNumberInAyah: word.word_number_in_ayah
                });
                // Play audio if available with improved error handling
                if (word.audio && word.audio.default) {
                  // Track audio loading attempts
                  let audioAttempts = 0;
                  const maxAttempts = 2;
                  
                  // Function to try loading audio with different paths
                  const tryPlayAudio = (audioPath) => {
                    audioAttempts++;
                    try {
                      const audio = new window.Audio(audioPath);
                      
                      // Add error handling for missing files
                      audio.addEventListener('error', (err) => {
                        console.warn(`Audio error (attempt ${audioAttempts}):`, err);
                        
                        // If primary path failed, try alternate path
                        if (audioAttempts === 1) {
                          // Try alternate path format (without /audio/ prefix)
                          tryPlayAudio(`${word.audio.default}`);
                        } else if (audioAttempts === 2) {
                          // Try absolute path with different folder structure
                          tryPlayAudio(`/audio/words/${word.audio.default}`);
                        } else if (audioAttempts > maxAttempts) {
                          // Only show user feedback after multiple attempts
                          console.error('Could not play audio after multiple attempts');
                          // Show subtle feedback instead of alert
                          const wordElement = e.currentTarget;
                          if (wordElement) {
                            // Add temporary visual feedback
                            const originalBackground = wordElement.style.background;
                            wordElement.style.background = 'rgba(255,0,0,0.1)';
                            setTimeout(() => {
                              wordElement.style.background = originalBackground;
                            }, 300);
                          }
                        }
                      });
                      
                      // Attempt to play
                      audio.play()
                        .then(() => {
                          console.log('Audio started:', audioPath);
                        })
                        .catch(err => {
                          if (err.name === 'NotSupportedError' || err.name === 'NotAllowedError') {
                            console.warn('Audio playback not allowed or supported');
                          } else if (err.name === 'AbortError') {
                            console.warn('Audio playback aborted');
                          } else {
                            console.warn('Audio play error:', err);
                            
                            // Try fallback if this is first attempt
                            if (audioAttempts <= maxAttempts) {
                              if (audioAttempts === 1) {
                                tryPlayAudio(`${word.audio.default}`);
                              } else {
                                tryPlayAudio(`/audio/words/${word.audio.default}`);
                              }
                            }
                          }
                        });
                    } catch (err) {
                      console.warn('Audio object error:', err);
                    }
                  };
                  
                  // Start with default path
                  tryPlayAudio(`/audio/${word.audio.default}`);
                } else {
                  console.log('No audio property found for word:', word);
                  
                  // Try to construct a default audio path based on position
                  const surahNum = surah.number;
                  const ayahNum = ayah.numberInSurah;
                  const wordNum = word.word_number_in_ayah;
                  const defaultAudioFile = `${String(surahNum).padStart(3, '0')}_${String(ayahNum).padStart(3, '0')}_${String(wordNum).padStart(3, '0')}.mp3`;
                  
                  try {
                    const audio = new window.Audio(`/audio/${defaultAudioFile}`);
                    audio.play().catch(() => {
                      // Silent failure
                      console.warn('Could not play fallback audio:', defaultAudioFile);
                    });
                  } catch (err) {
                    // Silent failure
                    console.warn('Could not create fallback audio object');
                  }
                }
              }}
            >
              <span
                dangerouslySetInnerHTML={{
                  __html: require('../../utils/tajweedParser').parseTajweedText(arabicWithTajweed, true, false)
                }}
              />
              {isSelected && (word.word_translation || word.word_transliteration) && (
                <span
                  ref={bubbleRef}
                  style={{
                    position: 'absolute',
                    bottom: '2.2em',
                    right: '-10px',
                    background: 'rgba(255,255,255,0.98)',
                    color: '#222',
                    border: '1.5px solid #1976d2',
                    borderRadius: 8,
                    boxShadow: '0 2px 8px 0 rgba(33,150,243,0.12)',
                    padding: '4px 10px',
                    zIndex: 20,
                    minWidth: 80,
                    maxWidth: 180,
                    fontSize: '0.82em',
                    whiteSpace: 'pre-line',
                    textAlign: 'center',
                    fontFamily: 'inherit',
                    animation: 'fadeInBubble 0.18s cubic-bezier(.4,0,.2,1)',
                  }}
                >
                  <div style={{fontWeight:'bold',color:'#1976d2',fontSize:'0.98em',marginBottom:2}}>{word.word_translation}</div>
                  {word.word_transliteration && (
                    <div style={{ fontSize: '0.78em', color: '#555', marginTop: 2, fontStyle:'italic' }}>
                      {word.word_transliteration}
                    </div>
                  )}
                  <span
                    style={{
                      position: 'absolute',
                      bottom: '-10px',
                      right: 24,
                      width: 0,
                      height: 0,
                      borderLeft: '8px solid transparent',
                      borderRight: '8px solid transparent',
                      borderTop: '10px solid #1976d2',
                    }}
                  />
                </span>
              )}
            </span>
          );
        })
      ) : ayahText && typeof ayahText === 'string' && ayahText.includes('|') ? (
        ayahText.split('$').filter(Boolean).map((block, idx, arr) => {
          const parts = block.split('|');
          const arabic = parts[0] || '';
          const translation = parts[1] || '';
          const surahNum = surah.number;
          const ayahNum = ayah.numberInSurah;
          const wordNum = idx + 1;
          const audioFile = `${String(surahNum).padStart(3, '0')}_${String(ayahNum).padStart(3, '0')}_${String(wordNum).padStart(3, '0')}.mp3`;
          const isSelected = selectedWord &&
            selectedWord.surahNumber === surahNum &&
            selectedWord.ayahNumber === ayahNum &&
            selectedWord.wordNumberInAyah === wordNum;
          const KIDS_COLORS = [
            '#e57373', '#64b5f6', '#81c784', '#ffd54f', '#ba68c8',
            '#4db6ac', '#ffb74d', '#a1887f', '#90a4ae', '#f06292',
          ];
          return (
            <span
              key={wordNum}
              style={{
                cursor: 'pointer',
                color: isSelected ? '#fff' : KIDS_COLORS[(wordNum-1) % KIDS_COLORS.length],
                background: isSelected ? 'linear-gradient(90deg,#1976d2 60%,#42a5f5 100%)' : 'transparent',
                borderRadius: '8px',
                padding: '0 8px',
                marginLeft: 6,
                fontWeight: 'bold',
                transition: 'background 0.2s, color 0.2s',
                display: 'inline-block',
                position: 'relative',
                boxShadow: isSelected ? '0 2px 12px 0 rgba(33,150,243,0.15)' : 'none',
                direction: 'rtl',
                fontSize: settings.fontSize,
              }}
              title={translation || ''}
              onClick={e => {
                e.stopPropagation();
                setSelectedWord && setSelectedWord({
                  surahNumber: surahNum,
                  ayahNumber: ayahNum,
                  wordNumberInAyah: wordNum
                });
                // Play audio for quran-kids
                try {
                  const audio = new window.Audio(`/audio/${audioFile}`);
                  audio.play().then(() => {
                    console.log('Audio started:', audioFile);
                  }).catch(err => {
                    console.log('Audio play error:', err);
                    alert('Audio could not be played. See console for details.');
                  });
                } catch (err) {
                  console.log('Audio object error:', err);
                  alert('Audio object could not be created. See console for details.');
                }
              }}
            >
              {arabic}
              {isSelected && translation && (
                <span
                  ref={bubbleRef}
                  style={{
                    position: 'absolute',
                    bottom: '2.2em',
                    right: '-10px',
                    background: 'rgba(255,255,255,0.98)',
                    color: '#222',
                    border: '1.5px solid #1976d2',
                    borderRadius: 8,
                    boxShadow: '0 2px 8px 0 rgba(33,150,243,0.12)',
                    padding: '4px 10px',
                    zIndex: 20,
                    minWidth: 80,
                    maxWidth: 180,
                    fontSize: '0.82em',
                    whiteSpace: 'pre-line',
                    textAlign: 'center',
                    fontFamily: 'inherit',
                    animation: 'fadeInBubble 0.18s cubic-bezier(.4,0,.2,1)',
                  }}
                >
                  <div style={{fontWeight:'bold',color:'#1976d2',fontSize:'0.98em',marginBottom:2}}>{translation}</div>
                  <span
                    style={{
                      position: 'absolute',
                      bottom: '-10px',
                      right: 24,
                      width: 0,
                      height: 0,
                      borderLeft: '8px solid transparent',
                      borderRight: '8px solid transparent',
                      borderTop: '10px solid #1976d2',
                    }}
                  />
                </span>
              )}
            </span>
          );
        })
      ) : null}
    </div>
  );
};

export default WordByWordSpeechBubble;
