import Ayah from './Ayah';

/**
 * Represents a Surah (chapter) of the Quran
 */
class Surah {
  constructor(data) {
    this.number = data.number;
    this.name = data.name;
    this.englishName = data.englishName;
    this.englishNameTranslation = data.englishNameTranslation;
    this.revelationType = data.revelationType;
    this.ayahs = data.ayahs?.map(ayah => new Ayah(ayah)) || [];
  }
  
  // Get ayah by number within surah
  getAyah(numberInSurah) {
    return this.ayahs.find(ayah => ayah.numberInSurah === numberInSurah);
  }
  
  // Get a range of ayahs
  getAyahRange(startNumber, endNumber) {
    return this.ayahs.filter(
      ayah => ayah.numberInSurah >= startNumber && 
              ayah.numberInSurah <= endNumber
    );
  }
  
  // Whether this surah begins with Bismillah
  hasBismillah() {
    // All surahs start with Bismillah except Surah 9 (At-Tawba)
    return this.number !== 9;
  }
}

export default Surah;