// src/components/quran/SurahList.js
import React, { useState } from 'react';
import {
  Box,
  Paper,
  List,
  ListItemButton,
  ListItemText,
  Typography,
  TextField,
  InputAdornment,
  Divider
} from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';

const SurahList = ({ surahs, selectedSurah, onSelectSurah }) => {
  const [searchQuery, setSearchQuery] = useState('');
  
  // Filter surahs based on search query
  const filteredSurahs = surahs.filter(surah => {
    const query = searchQuery.toLowerCase();
    return (
      surah.englishName.toLowerCase().includes(query) ||
      surah.englishNameTranslation.toLowerCase().includes(query) ||
      surah.number.toString().includes(query)
    );
  });
  
  return (
    <Paper elevation={3} sx={{ borderRadius: 2 }}>
      <Box sx={{ p: 2 }}>
        <Typography variant="h6" component="h2" sx={{ mb: 2 }}>
          Surahs
        </Typography>
        
        <TextField
          fullWidth
          size="small"
          placeholder="Search Surah"
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SearchIcon />
              </InputAdornment>
            ),
          }}
          sx={{ mb: 2 }}
        />
        
        <Divider />
        
        <List 
          sx={{ 
            maxHeight: 'calc(100vh - 220px)', 
            overflow: 'auto', 
            pt: 0 
          }} 
          dense
        >
          {filteredSurahs.map((surah) => (
            <ListItemButton
              key={surah.number}
              selected={selectedSurah === surah.number}
              onClick={() => onSelectSurah(surah.number)}
            >
              <ListItemText
                primary={
                  <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
                    <Typography variant="body2">
                      {surah.number}. {surah.englishName}
                    </Typography>
                    <Typography 
                      variant="body2" 
                      sx={{ 
                        fontFamily: 'Scheherazade, serif',
                        fontSize: '1.1rem'
                      }}
                    >
                      {surah.name.split(' ')[1] || surah.name}
                    </Typography>
                  </Box>
                }
                secondary={surah.englishNameTranslation}
              />
            </ListItemButton>
          ))}
        </List>
      </Box>
    </Paper>
  );
};

export default SurahList;