// src/services/quranService.js
import Quran from '../models/Quran';

class QuranService {
  constructor() {
    this.loadedEditions = new Map();
    this.availableEditions = [
      { identifier: 'quran-tajweed', language: 'ar', name: 'Tajweed', type: 'quran' },
      { identifier: 'en.sahih', language: 'en', name: 'Sahih International', type: 'translation' },
      { identifier: 'ur.ahmed', language: 'ur', name: 'Ahmed Ali', type: 'translation' },
      { identifier: 'ar.muyassar', language: 'ar', name: ' ', type: 'tafsir' },
      { identifier: 'en.tafsir-ibn-kathir', language: 'en', name: 'Ibn Kathir', type: 'tafsir' },
    ];
  }
  
  /**
   * Get available Quran editions
   */
  getAvailableEditions() {
    return this.availableEditions;
  }
  
  /**
   * Load a specific Quran edition
   * @param {string} identifier - The edition identifier
   * @returns {Promise<Quran>} A promise resolving to the Quran object
   */
  async loadEdition(identifier) {
    // Check if we already have this edition loaded
    if (this.loadedEditions.has(identifier)) {
      return this.loadedEditions.get(identifier);
    }
    
    try {
      let data;
      
      // For local editions, load from local file
      if (
        identifier === 'quran-tajweed' ||
        identifier === 'quran-kids' ||
        identifier === 'quran-wordbyword-2'
      ) {
        const response = await fetch(`/assets/data/quran/${identifier}.json`);
        
        if (!response.ok) {
          throw new Error(`Failed to load Quran edition: ${identifier}`);
        }
        data = await response.json();
      } else {
        // For other editions, fetch from API
        const response = await fetch(`https://api.alquran.cloud/v1/quran/${identifier}`);
        if (!response.ok) {
          throw new Error(`Failed to load Quran edition: ${identifier}`);
        }
        data = await response.json();
      } 
      
      // Create Quran object
      const quran = new Quran(data.data);
      
      // Store in cache
      this.loadedEditions.set(identifier, quran);
      
      return quran;
    } catch (error) {
      console.error('Error loading Quran edition:', error);
      throw error;
    }
  }
  
  /**
   * Get audio URL for a specific ayah
   * @param {number} surahNumber - The surah number
   * @param {number} ayahNumber - The ayah number within the surah
   * @param {string} reciter - The reciter identifier
   * @param {number} bitrate - Audio bitrate (64, 128, 192, or 360)
   * @returns {string} The audio URL
   */
  getAudioUrl(surahNumber, ayahNumber, reciter = 'ar.alafasy', bitrate = 128) {
    // Validate the bitrate
    const validBitrates = [64, 128, 192, 320, 32, 48];
    const selectedBitrate = validBitrates.includes(bitrate) ? bitrate : 128;
    
    // We need the global ayah number for the audio URL
    let globalAyahNumber;
    
    // If we're given an ayah object with number property, use that directly
    if (typeof ayahNumber === 'object' && ayahNumber.number) {
      globalAyahNumber = ayahNumber.number;
    } else {
      // Otherwise calculate the global ayah number from surah and ayahNumber
      globalAyahNumber = this.getGlobalAyahNumber(surahNumber, ayahNumber);
    }
    
    return `https://cdn.islamic.network/quran/audio/${selectedBitrate}/${reciter}/${globalAyahNumber}.mp3`;
  }
  
  /**
   * Calculate global ayah number from surah number and ayah within surah
   * @param {number} surahNumber - The surah number (1-114)
   * @param {number} ayahInSurah - The ayah number within the surah
   * @returns {number} The global ayah number
   */
  getGlobalAyahNumber(surahNumber, ayahInSurah) {
    // Ayah counts for each surah in the Quran
    const ayahCounts = [
      7, 286, 200, 176, 120, 165, 206, 75, 129, 109, 123, 111, 43, 52, 99, 128, 
      111, 110, 98, 135, 112, 78, 118, 64, 77, 227, 93, 88, 69, 60, 34, 30, 73, 
      54, 45, 83, 182, 88, 75, 85, 54, 53, 89, 59, 37, 35, 38, 29, 18, 45, 60, 
      49, 62, 55, 78, 96, 29, 22, 24, 13, 14, 11, 11, 18, 12, 12, 30, 52, 52, 
      44, 28, 28, 20, 56, 40, 31, 50, 40, 46, 42, 29, 19, 36, 25, 22, 17, 19, 
      26, 30, 20, 15, 21, 11, 8, 8, 19, 5, 8, 8, 11, 11, 8, 3, 9, 5, 4, 7, 3, 
      6, 3, 5, 4, 5, 6
    ];
    
    let globalAyahNumber = ayahInSurah;
    
    // Add up the ayahs from all previous surahs
    for (let i = 0; i < surahNumber - 1; i++) {
      globalAyahNumber += ayahCounts[i];
    }
    
    return globalAyahNumber;
  }
  
  /**
   * Get the next ayah based on navigation mode and current ayah
   * @param {Object} ayah - Current ayah
   * @param {string} navigationMode - Current navigation mode (surah, page, juz, etc)
   * @param {Object} quranData - Quran data object
   * @returns {Object|null} - Next ayah or null if at boundary
   */
  getNextAyah(ayah, navigationMode, quranData) {
    if (!ayah || !quranData) return null;
    
    const surahNumber = ayah.surahNumber || ayah.surah;
    const surah = quranData.getSurah(surahNumber);
    
    if (!surah) return null;
    
    // For surah navigation, simply get the next ayah in the surah
    if (navigationMode === 'surah') {
      const nextAyahNumber = ayah.numberInSurah + 1;
      if (nextAyahNumber <= surah.ayahs.length) {
        return surah.getAyah(nextAyahNumber);
      }
      return null; // End of surah
    }
    
    // For other navigation modes, we need to respect the boundaries
    const currentMarker = ayah[navigationMode];
    const nextAyahNumber = ayah.number + 1;
    
    // Try to find the next ayah globally
    for (let s = surahNumber; s <= 114; s++) {
      const searchSurah = quranData.getSurah(s);
      if (!searchSurah) continue;
      
      // Start from the next ayah in the current surah, or first ayah in next surahs
      const startAyah = s === surahNumber ? ayah.numberInSurah + 1 : 1;
      
      for (let a = startAyah; a <= searchSurah.ayahs.length; a++) {
        const checkAyah = searchSurah.getAyah(a);
        
        // If the next ayah has the same marker, it's in the same boundary
        if (checkAyah && checkAyah[navigationMode] === currentMarker) {
          return checkAyah;
        }
        
        // If we find an ayah with a different marker, we've crossed the boundary
        if (checkAyah && checkAyah[navigationMode] !== currentMarker) {
          return null;
        }
      }
    }
    
    return null; // No next ayah found within the boundary
  }
}

export default new QuranService();