// src/components/quran/QuranNavigationPanel.js
import React, { useState } from 'react';
import {
  Box,
  Tabs,
  Tab,
  Grid,
  Card,
  CardActionArea,
  Typography,
  Divider,
  useTheme,
  alpha,
} from '@mui/material';
import MenuBookIcon from '@mui/icons-material/MenuBook'; // For Surah
import ViewDayIcon from '@mui/icons-material/ViewDay'; // For Page
import LooksOneIcon from '@mui/icons-material/LooksOne'; // For Juz
import AltRouteIcon from '@mui/icons-material/AltRoute'; // For Ruku
import FilterCenterFocusIcon from '@mui/icons-material/FilterCenterFocus'; // For Hizb
import ViewWeekIcon from '@mui/icons-material/ViewWeek'; // For Manzil

// Helper function to create the navigation items
const createNavigationItems = (mode) => {
  switch(mode) {
    case 'surah':
      // Return all 114 surahs
      return Array.from({ length: 114 }, (_, i) => ({
        number: i + 1,
        name: `Surah ${i + 1}` // In a real implementation, get the actual names
      }));
    case 'page':
      // Return all 604 pages
      return Array.from({ length: 604 }, (_, i) => ({
        number: i + 1,
        name: `Page ${i + 1}`
      }));
    case 'juz':
      // Return all 30 juz
      return Array.from({ length: 30 }, (_, i) => ({
        number: i + 1,
        name: `Juz ${i + 1}`
      }));
    case 'hizb':
      // Return all 60 hizb (including quarters)
      return Array.from({ length: 60 }, (_, i) => ({
        number: i + 1,
        name: `Hizb ${Math.floor(i/4) + 1}${i % 4 > 0 ? `.${i % 4}` : ''}`
      }));
    case 'ruku':
      // Return all 556 ruku sections
      return Array.from({ length: 556 }, (_, i) => ({
        number: i + 1,
        name: `Ruku ${i + 1}`
      }));
    case 'manzil':
      // Return all 7 manzils
      return Array.from({ length: 7 }, (_, i) => ({
        number: i + 1,
        name: `Manzil ${i + 1}`
      }));
    default:
      return [];
  }
};

// Get icon for navigation mode
const getNavigationIcon = (mode) => {
  switch(mode) {
    case 'surah': return <MenuBookIcon />;
    case 'page': return <ViewDayIcon />;
    case 'juz': return <LooksOneIcon />;
    case 'hizb': return <FilterCenterFocusIcon />;
    case 'ruku': return <AltRouteIcon />;
    case 'manzil': return <ViewWeekIcon />;
    default: return <MenuBookIcon />;
  }
};

const QuranNavigationPanel = ({ onNavigate }) => {
  const theme = useTheme();
  const [activeTab, setActiveTab] = useState('surah');
  const [items] = useState({
    surah: createNavigationItems('surah'),
    page: createNavigationItems('page'),
    juz: createNavigationItems('juz'),
    hizb: createNavigationItems('hizb'),
    ruku: createNavigationItems('ruku'),
    manzil: createNavigationItems('manzil'),
  });
  
  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };
  
  const handleItemClick = (item) => {
    onNavigate(activeTab, item.number);
  };
  
  return (
    <Box sx={{ width: '100%' }}>
      {/* Wrap Tabs in a nav element */}
      <Box 
        component="nav" 
        aria-label="Quran Navigation Type" 
        sx={{ borderBottom: 1, borderColor: 'divider' }}
      >
        <Tabs 
          value={activeTab} 
          onChange={handleTabChange} 
          variant="scrollable"
          scrollButtons="auto"
          allowScrollButtonsMobile
          aria-label="Select navigation mode" // Add label for the tab list itself
        >
          <Tab 
            icon={getNavigationIcon('surah')} 
            label="Surah" 
            value="surah" 
            iconPosition="start"
            id="nav-tab-surah" // Add ID
            aria-controls="nav-tabpanel-surah" // Link to potential panel
          />
          <Tab 
            icon={getNavigationIcon('page')} 
            label="Page" 
            value="page" 
            iconPosition="start"
            id="nav-tab-page"
            aria-controls="nav-tabpanel-page"
          />
          <Tab 
            icon={getNavigationIcon('juz')} 
            label="Juz" 
            value="juz" 
            iconPosition="start"
            id="nav-tab-juz"
            aria-controls="nav-tabpanel-juz"
          />
          <Tab 
            icon={getNavigationIcon('hizb')} 
            label="Hizb" 
            value="hizb" 
            iconPosition="start"
            id="nav-tab-hizb"
            aria-controls="nav-tabpanel-hizb"
          />
          <Tab 
            icon={getNavigationIcon('ruku')} 
            label="Ruku" 
            value="ruku" 
            iconPosition="start"
            id="nav-tab-ruku"
            aria-controls="nav-tabpanel-ruku"
          />
          <Tab 
            icon={getNavigationIcon('manzil')} 
            label="Manzil" 
            value="manzil" 
            iconPosition="start"
            id="nav-tab-manzil"
            aria-controls="nav-tabpanel-manzil"
          />
        </Tabs>
      </Box>
      
      {/* Tab Panel Content */}
      <Box 
        role="tabpanel" // Add role
        id={`nav-tabpanel-${activeTab}`} // Match ID with aria-controls
        aria-labelledby={`nav-tab-${activeTab}`} // Link panel to tab
        sx={{ mt: 2, maxHeight: '400px', overflow: 'auto', p: 1 }}
      >
        <Grid container spacing={1}>
          {items[activeTab].map((item) => (
            <Grid item xs={6} sm={4} md={3} lg={2} key={`${activeTab}-${item.number}`}>
              <Card 
                variant="outlined" 
                sx={{ 
                  transition: 'all 0.2s ease',
                  '&:hover': {
                    boxShadow: 2,
                    borderColor: theme.palette.primary.main,
                  }
                }}
              >
                <CardActionArea 
                  onClick={() => handleItemClick(item)}
                  aria-label={`Navigate to ${activeTab} ${item.number}`}
                  sx={{ 
                    p: 1,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    justifyContent: 'center',
                    height: '100%'
                  }}
                >
                  <Typography variant="body2" color="primary" fontWeight="medium">
                    {item.number}
                  </Typography>
                  <Typography variant="caption" align="center">
                    {item.name}
                  </Typography>
                </CardActionArea>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Box>
    </Box>
  );
};

export default QuranNavigationPanel;