// src/components/quran/QuranNavigationControls.js
import React, { useState } from 'react';
import {
  Box,
  Button,
  IconButton,
  Menu,
  MenuItem,
  TextField,
  InputAdornment,
  Tooltip,
  Typography,
  Divider,
  useTheme,
} from '@mui/material';
import NavigateBeforeIcon from '@mui/icons-material/NavigateBefore';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import ViewListIcon from '@mui/icons-material/ViewList';
import BookmarkIcon from '@mui/icons-material/Bookmark';
import SearchIcon from '@mui/icons-material/Search';

// Mapping data to connect navigation positions with surah/ayah positions
const quranNavigationMappings = {
  page: {
    // Example data - actual implementation will use the json data
    1: { surah: 1, ayah: 1 }, // Al-Fatiha
    2: { surah: 2, ayah: 1 }, // Al-Baqarah
    3: { surah: 2, ayah: 25 },
    // ... more page mappings
  },
  juz: {
    // All 30 juz mappings
    1: { surah: 1, ayah: 1 },
    2: { surah: 2, ayah: 142 },
    3: { surah: 2, ayah: 253 },
    // ... more juz mappings
  },
  hizb: {
    // All 60 hizb mappings (including hizb quarters)
    1: { surah: 1, ayah: 1 },
    "1.1": { surah: 2, ayah: 25 },
    "1.2": { surah: 2, ayah: 44 },
    "1.3": { surah: 2, ayah: 60 },
    2: { surah: 2, ayah: 75 },
    // ... more hizb mappings
  },
  ruku: {
    // Example data for some rukus (actual data would have all 556 rukus)
    1: { surah: 1, ayah: 1 },
    2: { surah: 2, ayah: 1 },
    3: { surah: 2, ayah: 8 },
    // ... more ruku mappings
  },
  manzil: {
    // All 7 manzil mappings
    1: { surah: 1, ayah: 1 },
    2: { surah: 5, ayah: 1 },
    3: { surah: 10, ayah: 1 },
    4: { surah: 17, ayah: 1 },
    5: { surah: 26, ayah: 1 },
    6: { surah: 37, ayah: 1 },
    7: { surah: 50, ayah: 1 },
  },
};

/**
 * Gets the surah and ayah for a given position in the specified navigation mode
 * @param {string} mode - The navigation mode (page, juz, hizb, ruku, manzil)
 * @param {number|string} position - The position in the navigation mode
 * @returns {Object|null} - The corresponding surah and ayah, or null if not found
 */
export const getQuranLocationFromPosition = (mode, position) => {
  if (!quranNavigationMappings[mode]) return null;
  
  const location = quranNavigationMappings[mode][position];
  return location || null;
};

/**
 * Component for Quran navigation controls
 */
const QuranNavigationControls = ({ 
  navigationMode = 'page', // Default to 'page' (singular)
  currentPosition,
  maxPosition,
  onNavigationModeChange,
  onPositionChange,
  onGoToSurah
}) => {
  const theme = useTheme();
  const [anchorEl, setAnchorEl] = useState(null);
  const [jumpToValue, setJumpToValue] = useState("");
  const menuId = 'navigation-mode-menu'; // ID for menu
  const jumpInputId = 'jump-to-input'; // ID for jump input
  
  const handleModeMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };
  
  const handleModeMenuClose = () => {
    setAnchorEl(null);
  };
  
  const handleModeSelect = (mode) => {
    onNavigationModeChange(mode);
    handleModeMenuClose();
  };
  
  const handlePrevious = () => {
    if (currentPosition > 1) {
      onPositionChange(currentPosition - 1);
      window.scrollTo({ top: 0, behavior: 'smooth' });
    }
  };
  
  const handleNext = () => {
    if (currentPosition < maxPosition) {
      onPositionChange(currentPosition + 1);
      window.scrollTo({ top: 0, behavior: 'smooth' });
    }
  };
  
  const handleJumpTo = () => {
    const value = parseInt(jumpToValue, 10);
    if (!isNaN(value) && value >= 1 && value <= maxPosition) {
      onPositionChange(value);
      setJumpToValue("");
    }
  };
  
  const getNavigationModeText = () => {
    switch(navigationMode) {
      case 'page': return 'Page';
      case 'juz': return 'Juz';
      case 'hizb': return 'Hizb';
      case 'ruku': return 'Ruku';
      case 'manzil': return 'Manzil';
      case 'surah': return 'Surah';
      default: return 'Navigation';
    }
  };
  
  const getMaxPositionByMode = () => {
    switch(navigationMode) {
      case 'page': return 604; // Total pages in the standard Medina Mushaf
      case 'juz': return 30;
      case 'hizb': return 60;
      case 'ruku': return 556;
      case 'manzil': return 7;
      case 'surah': return 114;
      default: return 1;
    }
  };
  
  const getPlaceholderText = () => {
    if (!navigationMode || typeof navigationMode !== 'string') {
      return "Go to position";
    }
    
    return `Go to ${navigationMode}`;
  };
  
  return (
    <Box 
      component="nav" // Use nav element
      aria-label="Quran Navigation Controls" // Add aria-label
      sx={{ 
        display: 'flex', 
        alignItems: 'center',
        justifyContent: 'space-between', 
        flexWrap: 'wrap',
        gap: 1,
        p: 1
      }}
    >
      {/* Navigation Mode Selector */}
      <Box sx={{ display: 'flex', alignItems: 'center' }}>
        <Button 
          variant="outlined"
          onClick={handleModeMenuOpen}
          endIcon={<KeyboardArrowDownIcon />}
          size="small"
          sx={{ whiteSpace: 'nowrap' }}
          aria-controls={menuId} // Link button to menu
          aria-haspopup="true" // Indicate it opens a menu
          aria-expanded={Boolean(anchorEl) ? 'true' : undefined} // Indicate if menu is open
        >
          {getNavigationModeText()}
        </Button>
        
        <Menu
          id={menuId} // Add ID to menu
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          onClose={handleModeMenuClose}
        >
          <MenuItem onClick={() => handleModeSelect('page')}>Page</MenuItem>
          <MenuItem onClick={() => handleModeSelect('juz')}>Juz</MenuItem>
          <MenuItem onClick={() => handleModeSelect('hizb')}>Hizb</MenuItem>
          <MenuItem onClick={() => handleModeSelect('ruku')}>Ruku</MenuItem>
          <MenuItem onClick={() => handleModeSelect('manzil')}>Manzil</MenuItem>
          <MenuItem onClick={() => handleModeSelect('surah')}>Surah</MenuItem>
        </Menu>
        
        <Typography variant="body1" sx={{ ml: 2, mr: 1 }} aria-live="polite"> {/* Announce changes */}
          {currentPosition || 1} of {getMaxPositionByMode()}
        </Typography>
      </Box>
      
      {/* Jump to input */}
      <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
        {/* Add a visually hidden label for accessibility */}
        <Typography component="label" htmlFor={jumpInputId} sx={{ ...visuallyHidden }}>
          {`Jump to ${navigationMode}`}
        </Typography>
        <TextField 
          id={jumpInputId} // Add ID to input
          size="small"
          placeholder={getPlaceholderText()}
          value={jumpToValue}
          onChange={(e) => setJumpToValue(e.target.value)}
          onKeyPress={(e) => e.key === 'Enter' && handleJumpTo()}
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <IconButton 
                  size="small" 
                  onClick={handleJumpTo}
                  edge="end"
                  aria-label={`Jump to specified ${navigationMode}`}
                >
                  <SearchIcon fontSize="small" />
                </IconButton>
              </InputAdornment>
            ),
          }}
          sx={{ width: '120px' }}
        />
        
        {/* Navigation buttons */}
        <Box>
          <Tooltip title="Previous">
            <IconButton 
              onClick={handlePrevious} 
              disabled={currentPosition <= 1}
              size="small"
              aria-label={`Go to previous ${navigationMode}`}
            >
              <NavigateBeforeIcon />
            </IconButton>
          </Tooltip>
          
          <Tooltip title="Next">
            <IconButton 
              onClick={handleNext} 
              disabled={currentPosition >= maxPosition}
              size="small"
              aria-label={`Go to next ${navigationMode}`}
            >
              <NavigateNextIcon />
            </IconButton>
          </Tooltip>
          
          <Tooltip title="Surah List">
            <IconButton 
              onClick={() => onGoToSurah()} 
              size="small"
              aria-label="Open Surah list panel"
            >
              <ViewListIcon />
            </IconButton>
          </Tooltip>
          
          <Tooltip title="Bookmarks">
            <IconButton size="small" aria-label="View Bookmarks" disabled> {/* Add aria-label, disable for now */}
              <BookmarkIcon />
            </IconButton>
          </Tooltip>
        </Box>
      </Box>
    </Box>
  );
};

// Helper style for visually hidden labels
const visuallyHidden = {
  border: 0,
  clip: 'rect(0 0 0 0)',
  height: 1,
  margin: -1,
  overflow: 'hidden',
  padding: 0,
  position: 'absolute',
  width: 1,
};

export default QuranNavigationControls;