// src/components/quran/EnhancedAyahView.js
import React, { useEffect, useState, memo } from 'react'; // Import memo
import {
  Box,
  Typography,
  Paper,
  IconButton,
  Tooltip,
  Chip,
  useTheme,
  alpha
} from '@mui/material';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import PauseIcon from '@mui/icons-material/Pause';
import BookmarkBorderIcon from '@mui/icons-material/BookmarkBorder';
import BookmarkIcon from '@mui/icons-material/Bookmark';
import AltRouteIcon from '@mui/icons-material/AltRoute';  // For Ruku
import FilterCenterFocusIcon from '@mui/icons-material/FilterCenterFocus'; // For Hizb
import LooksOneIcon from '@mui/icons-material/LooksOne'; // For Juz
import TouchAppIcon from '@mui/icons-material/TouchApp'; // For Sajda
import InfoIcon from '@mui/icons-material/Info';
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline'; // For read indicator
import { motion } from 'framer-motion';

import { parseTajweedText } from '../../utils/tajweedParser';
import quranProgressTracker from '../../utils/QuranProgressTracker';
import WordByWordSpeechBubble from './WordByWordSpeechBubble';
import WordByWordSpeechBubbleKids from './WordByWordSpeechBubbleKids';

// Special markers data for Quran
const sajdaAyahs = [
  { surah: 7, ayah: 206, type: 'recommended' },
  { surah: 13, ayah: 15, type: 'recommended' },
  { surah: 16, ayah: 50, type: 'recommended' },
  { surah: 17, ayah: 109, type: 'recommended' },
  { surah: 19, ayah: 58, type: 'recommended' },
  { surah: 22, ayah: 18, type: 'recommended' },
  { surah: 22, ayah: 77, type: 'recommended' },
  { surah: 25, ayah: 60, type: 'recommended' },
  { surah: 27, ayah: 26, type: 'recommended' },
  { surah: 32, ayah: 15, type: 'obligatory' },
  { surah: 38, ayah: 24, type: 'recommended' },
  { surah: 41, ayah: 38, type: 'obligatory' },
  { surah: 53, ayah: 62, type: 'obligatory' },
  { surah: 84, ayah: 21, type: 'recommended' },
  { surah: 96, ayah: 19, type: 'obligatory' }
];

// Example data for rukus, hizbs, and juz (this would be expanded in a real implementation)
const rukuEndpoints = [
  { surah: 1, ayah: 7 },
  { surah: 2, ayah: 7 },
  { surah: 2, ayah: 16 },
  // ... more ruku endpoints
];

const hizbQuarters = [
  { surah: 1, ayah: 1 },
  { surah: 2, ayah: 25 },
  { surah: 2, ayah: 44 },
  // ... more hizb quarters
];

const juzStartPoints = [
  { surah: 1, ayah: 1 },
  { surah: 2, ayah: 142 },
  { surah: 2, ayah: 253 },
  // ... more juz points
];

/**
 * Checks if an ayah has a special mark
 * @param {number} surah - Surah number
 * @param {number} ayah - Ayah number
 * @param {Array} marksList - List of marks to check against
 * @returns {Object|null} - The mark object or null if not found
 */
const getAyahMark = (surah, ayah, marksList) => {
  return marksList.find(mark => mark.surah === surah && mark.ayah === ayah) || null;
};

// Animation variant for playing ayah
const playingVariant = {
  initial: { scale: 1, boxShadow: '0 0 0px rgba(0,0,0,0)' },
  animate: {
    scale: [1, 1.03, 1],
    boxShadow: [
      '0 0 0px rgba(0,0,0,0)',
      '0 0 18px rgba(25, 118, 210, 0.25)',
      '0 0 0px rgba(0,0,0,0)'
    ],
    transition: { duration: 0.8, repeat: Infinity, repeatType: 'loop', ease: 'easeInOut' }
  },
  rest: { scale: 1, boxShadow: '0 0 0px rgba(0,0,0,0)' }
};

/**
 * Enhanced component for displaying Ayah with special markers
 */
// Wrap component definition with memo
const EnhancedAyahView = memo(({ 
  ayah, 
  surah, 
  settings, 
  translation, 
  tafsir, 
  isPlaying,
  isBookmarked,
  onPlay, 
  onPause,
  onToggleBookmark,
  onClick,
  selectedWord,
  setSelectedWord
}) => {
  const theme = useTheme();
  const [isVisible, setIsVisible] = useState(false);
  const [hasMarkedAsRead, setHasMarkedAsRead] = useState(false);

  // Use Intersection Observer to detect when ayah is visible on screen
  useEffect(() => {
    if (!surah || !ayah) return;

    const observer = new IntersectionObserver(
      ([entry]) => {
        setIsVisible(entry.isIntersecting);
      },
      {
        threshold: 0.7, // At least 70% of the ayah must be visible
        rootMargin: '0px'
      }
    );

    // Find the element to observe
    const ayahElement = document.getElementById(`ayah-${surah.number}-${ayah.numberInSurah}`);
    if (ayahElement) {
      observer.observe(ayahElement);
    }

    return () => {
      if (ayahElement) {
        observer.unobserve(ayahElement);
      }
    };
  }, [surah?.number, ayah?.numberInSurah, surah, ayah]);
  
  // Mark as read only when the ayah is visible and hasn't been marked yet
  useEffect(() => {
    if (isVisible && !hasMarkedAsRead && surah && ayah) {
      const timer = setTimeout(() => {
        quranProgressTracker.markAsRead(surah.number, ayah.numberInSurah);
        setHasMarkedAsRead(true);
      }, 3000); // Mark as read after 3 seconds of viewing
      
      return () => clearTimeout(timer);
    }
  }, [isVisible, hasMarkedAsRead, surah?.number, ayah?.numberInSurah, surah, ayah]);
  
  // Mark ayah as read immediately when played
  useEffect(() => {
    if (isPlaying && surah && ayah && !hasMarkedAsRead) {
      quranProgressTracker.markAsRead(surah.number, ayah.numberInSurah);
      setHasMarkedAsRead(true);
    }
  }, [isPlaying, surah?.number, ayah?.numberInSurah, hasMarkedAsRead, surah, ayah]);
  
  // Reset the hasMarkedAsRead state if the ayah changes
  useEffect(() => {
    setHasMarkedAsRead(false);
  }, [surah?.number, ayah?.numberInSurah, surah, ayah]);

  // Effect to handle scrolling to the ayah when isPlaying becomes true
  useEffect(() => {
    if (isPlaying && surah && ayah) {
      // Use a small timeout to ensure DOM is updated first
      const scrollTimeout = setTimeout(() => {
        const ayahElement = document.getElementById(`ayah-${surah.number}-${ayah.numberInSurah}`);
        if (ayahElement) {
          // Add highlight animation class and scroll to the ayah with smooth animation
          ayahElement.classList.add('highlight-ayah-animation');
          ayahElement.scrollIntoView({ 
            behavior: 'smooth', 
            block: 'center'
          });
        }
      }, 300);

      return () => clearTimeout(scrollTimeout);
    }
  }, [isPlaying, surah?.number, ayah?.numberInSurah, surah, ayah]);

  // Clean up highlight animation when isPlaying is false
  useEffect(() => {
    if (!isPlaying && surah && ayah) {
      const ayahElement = document.getElementById(`ayah-${surah.number}-${ayah.numberInSurah}`);
      if (ayahElement) {
        ayahElement.classList.remove('highlight-ayah-animation');
      }
    }
  }, [isPlaying, surah?.number, ayah?.numberInSurah, surah, ayah]);
  
  // Check if this ayah has been read
  const hasBeenRead = surah && ayah ? quranProgressTracker.isAyahRead(surah.number, ayah.numberInSurah) : false;
  
  // Check if this ayah has any special markers
  const sajdaMark = getAyahMark(surah?.number, ayah?.numberInSurah, sajdaAyahs);
  const rukuMark = getAyahMark(surah?.number, ayah?.numberInSurah, rukuEndpoints);
  const hizbMark = getAyahMark(surah?.number, ayah?.numberInSurah, hizbQuarters);
  const juzMark = getAyahMark(surah?.number, ayah?.numberInSurah, juzStartPoints);
  
  // Has any marker
  const hasSpecialMark = sajdaMark || rukuMark || hizbMark || juzMark;
  
  return (
    <>
      <motion.div
        variants={playingVariant}
        initial="initial"
        animate={isPlaying ? 'animate' : 'rest'}
        style={{ borderRadius: 12 }}
      >
        <Paper
          elevation={1}
          sx={{
            p: 2,
            mb: 2,
            borderRadius: 2,
            borderLeft: hasSpecialMark ? `4px solid ${theme.palette.primary.main}` : 
                       hasBeenRead ? `4px solid ${theme.palette.success.light}` : 'none',
            bgcolor: isPlaying ? alpha(theme.palette.primary.main, 0.05) : 
                    hasBeenRead ? alpha(theme.palette.success.light, 0.03) : 'background.paper',
            transition: 'background-color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease',
            '&:hover': {
              bgcolor: alpha(theme.palette.primary.main, 0.03),
            },
            '&.highlight-ayah-animation': {
              boxShadow: `0 0 15px ${alpha(theme.palette.primary.main, 0.5)}`,
              transform: 'scale(1.01)',
            }
          }}
          onClick={onClick}
          id={`ayah-${surah?.number}-${ayah?.numberInSurah}`}
          className={isPlaying ? 'highlight-ayah-animation' : ''}
        >
          {/* Ayah number and controls */}
          <Box sx={{ 
            display: 'flex', 
            justifyContent: 'space-between',
            alignItems: 'center',
            mb: 1
          }}>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <Box
                sx={{
                  minWidth: '36px',
                  height: '36px',
                  borderRadius: '50%',
                  bgcolor: hasBeenRead ? alpha(theme.palette.success.light, 0.2) : 
                           (theme.palette.mode === 'dark' ? 'grey.800' : 'grey.100'),
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  mr: 1
                }}
                aria-label={`Ayah number ${ayah.numberInSurah}`}
              >
                <Typography variant="body2">
                  {ayah.numberInSurah}
                </Typography>
              </Box>
              
              {/* Display special markers as chips */}
              {sajdaMark && (
                <Tooltip title={`Sajda ${sajdaMark.type === 'obligatory' ? '(Obligatory)' : '(Recommended)'}`}>
                  <Chip
                    icon={<TouchAppIcon />}
                    label="Sajda"
                    size="small"
                    color={sajdaMark.type === 'obligatory' ? 'secondary' : 'primary'}
                    sx={{ mr: 1, fontSize: '0.75rem' }}
                  />
                </Tooltip>
              )}
              
              {rukuMark && (
                <Tooltip title="Ruku End">
                  <Chip
                    icon={<AltRouteIcon />}
                    label="Ruku"
                    size="small"
                    color="primary"
                    variant="outlined"
                    sx={{ mr: 1, fontSize: '0.75rem' }}
                  />
                </Tooltip>
              )}
              
              {hizbMark && (
                <Tooltip title="Hizb Quarter">
                  <Chip
                    icon={<FilterCenterFocusIcon />}
                    label="Hizb"
                    size="small"
                    color="primary"
                    variant="outlined"
                    sx={{ mr: 1, fontSize: '0.75rem' }}
                  />
                </Tooltip>
              )}
              
              {juzMark && (
                <Tooltip title="Juz Start">
                  <Chip
                    icon={<LooksOneIcon />}
                    label="Juz"
                    size="small"
                    color="primary"
                    variant="outlined"
                    sx={{ mr: 1, fontSize: '0.75rem' }}
                  />
                </Tooltip>
              )}
              
              {/* Read indicator */}
              {hasBeenRead && (
                <Tooltip title="You've read this ayah">
                  <Chip
                    icon={<CheckCircleOutlineIcon />}
                    label="Read"
                    size="small"
                    color="success"
                    variant="outlined"
                    sx={{ mr: 1, fontSize: '0.75rem' }}
                  />
                </Tooltip>
              )}
            </Box>
            
            <Box>
              <Tooltip title={isPlaying ? "Pause" : "Play"}>
                <IconButton 
                  size="small" 
                  onClick={(e) => {
                    e.stopPropagation();
                    isPlaying ? onPause() : onPlay();
                  }}
                  aria-label={isPlaying ? `Pause recitation of Surah ${surah?.number} Ayah ${ayah?.numberInSurah}` : `Play recitation of Surah ${surah?.number} Ayah ${ayah?.numberInSurah}`}
                  aria-pressed={isPlaying}
                >
                  {isPlaying ? <PauseIcon /> : <PlayArrowIcon />}
                </IconButton>
              </Tooltip>
              
              <Tooltip title={isBookmarked ? "Remove bookmark" : "Add bookmark"}>
                <IconButton 
                  size="small" 
                  onClick={(e) => {
                    e.stopPropagation();
                    onToggleBookmark();
                  }}
                  aria-label={isBookmarked ? `Remove bookmark for Surah ${surah?.number} Ayah ${ayah?.numberInSurah}` : `Add bookmark for Surah ${surah?.number} Ayah ${ayah?.numberInSurah}`}
                  aria-pressed={isBookmarked}
                >
                  {isBookmarked ? <BookmarkIcon color="primary" /> : <BookmarkBorderIcon />}
                </IconButton>
              </Tooltip>
            </Box>
          </Box>
          
          {/* Arabic text */}
          {(settings.quranEdition === 'quran-wordbyword-2') && ayah.text ? (
            <WordByWordSpeechBubble
              ayah={ayah}
              surah={surah}
              selectedWord={selectedWord}
              setSelectedWord={setSelectedWord}
              settings={settings}
            />
          ) : settings.quranEdition === 'quran-kids' && ayah.text ? (
            <WordByWordSpeechBubbleKids
              ayah={ayah}
              surah={surah}
              selectedWord={selectedWord}
              setSelectedWord={setSelectedWord}
              settings={settings}
            />
          ) : (
            <Typography
              variant="h6"
              component="div"
              lang="ar" // Add lang attribute for Arabic text
              sx={{
                fontFamily: settings.fontFamily,
                fontSize: settings.fontSize,
                fontWeight: settings.fontWeight || 'bold',
                direction: 'rtl',
                lineHeight: 2,
                mb: 2,
                textAlign: 'right',
                wordBreak: 'keep-all',
                textRendering: 'optimizeLegibility',
                WebkitFontSmoothing: 'antialiased',
              }}
              dangerouslySetInnerHTML={{
                __html: parseTajweedText(
                  ayah.tajweedText || ayah.text,
                  settings.showTajweed,
                  false
                )
              }}
            />
          )}
          
          {/* Translation if enabled */}
          {settings.showTranslation && translation && (
            <Box sx={{ mt: 2 }}>
              <Typography variant="body1" gutterBottom>
                {translation.text}
              </Typography>
              
              {settings.showTransliteration && (
                <Typography 
                  variant="body2" 
                  sx={{ 
                    fontStyle: 'italic',
                    color: 'text.secondary',
                    mb: 1
                  }}
                >
                  {translation.transliteration || "Transliteration not available"}
                </Typography>
              )}
            </Box>
          )}
          
          {/* Tafsir if enabled */}
          {settings.showTafsir && tafsir && (
            <Box sx={{ 
              mt: 2, 
              pt: 1,
              borderTop: `1px solid ${theme.palette.divider}` 
            }}>
              <Typography variant="subtitle2" gutterBottom>
                Tafsir:
              </Typography>
              <Typography variant="body2">
                {tafsir.text}
              </Typography>
            </Box>
          )}
        </Paper>
      </motion.div>
    </>
  );
}); // Close memo wrapper

export default EnhancedAyahView;