import React, { createContext, useState, useContext, useEffect } from 'react';

const QuranProgressContext = createContext();

export const QuranProgressProvider = ({ children }) => {
  const [progress, setProgress] = useState(() => {
    // Try to load saved progress from localStorage
    const savedProgress = localStorage.getItem('quranReadingProgress');
    return savedProgress ? JSON.parse(savedProgress) : {
      // Track read surahs and ayahs
      readSurahs: {},
      readAyahs: {},
      // Track time spent reading
      timeSpent: 0,
      lastReadTimestamp: null,
      // Track completion percentages
      overallCompletion: 0,
      // Track reading streak
      currentStreak: 0,
      lastReadDate: null,
      // Custom reading goals
      dailyGoal: 5, // Default: 5 ayahs per day
      weeklyGoal: 1, // Default: 1 juz per week
    };
  });

  // Save progress to localStorage whenever it changes
  useEffect(() => {
    localStorage.setItem('quranReadingProgress', JSON.stringify(progress));
  }, [progress]);

  // Mark an ayah as read
  const markAyahAsRead = (surahNumber, ayahNumber) => {
    setProgress(prev => {
      const surahKey = `${surahNumber}`;
      const ayahKey = `${surahNumber}:${ayahNumber}`;
      
      // Update read ayahs
      const readAyahs = { ...prev.readAyahs, [ayahKey]: Date.now() };
      
      // Update read surahs data
      const readSurahs = { ...prev.readSurahs };
      if (!readSurahs[surahKey]) {
        readSurahs[surahKey] = { 
          totalRead: 0,
          lastRead: Date.now(),
          ayahsRead: {}
        };
      }
      
      readSurahs[surahKey].totalRead = readSurahs[surahKey].totalRead + 1;
      readSurahs[surahKey].lastRead = Date.now();
      readSurahs[surahKey].ayahsRead = { 
        ...readSurahs[surahKey].ayahsRead, 
        [ayahNumber]: Date.now() 
      };

      // Calculate new overall completion percentage
      // Simple approximation: count unique ayahs read / total ayahs (6236)
      const totalAyahs = 6236;
      const uniqueAyahsRead = Object.keys(readAyahs).length;
      const overallCompletion = Math.min(100, Math.round((uniqueAyahsRead / totalAyahs) * 100 * 10) / 10);

      // Update reading streak
      const today = new Date().toDateString();
      const lastReadDate = prev.lastReadDate;
      let currentStreak = prev.currentStreak;
      
      if (lastReadDate !== today) {
        if (new Date(lastReadDate).toDateString() === new Date(Date.now() - 86400000).toDateString()) {
          // Yesterday was the last reading, continue the streak
          currentStreak += 1;
        } else if (!lastReadDate) {
          // First time reading
          currentStreak = 1;
        } else {
          // Streak broken, start over
          currentStreak = 1;
        }
      }

      return {
        ...prev,
        readSurahs,
        readAyahs,
        overallCompletion,
        lastReadTimestamp: Date.now(),
        timeSpent: prev.timeSpent + 10, // Add 10 seconds (conservative estimate)
        currentStreak,
        lastReadDate: today,
      };
    });
  };

  // Check if an ayah has been read
  const isAyahRead = (surahNumber, ayahNumber) => {
    const ayahKey = `${surahNumber}:${ayahNumber}`;
    return !!progress.readAyahs[ayahKey];
  };

  // Mark multiple ayahs as read (e.g., when a page is viewed)
  const markAyahsAsRead = (ayahs) => {
    if (!ayahs || ayahs.length === 0) return;
    
    ayahs.forEach(ayah => {
      if (ayah.surahNumber && ayah.numberInSurah) {
        markAyahAsRead(ayah.surahNumber, ayah.numberInSurah);
      }
    });
  };

  // Reset progress
  const resetProgress = () => {
    const defaultProgress = {
      readSurahs: {},
      readAyahs: {},
      timeSpent: 0,
      lastReadTimestamp: null,
      overallCompletion: 0,
      currentStreak: 0,
      lastReadDate: null,
      dailyGoal: progress.dailyGoal,
      weeklyGoal: progress.weeklyGoal,
    };
    
    setProgress(defaultProgress);
    localStorage.setItem('quranReadingProgress', JSON.stringify(defaultProgress));
  };

  // Update reading goals
  const updateGoals = (dailyGoal, weeklyGoal) => {
    setProgress(prev => ({
      ...prev,
      dailyGoal: dailyGoal || prev.dailyGoal,
      weeklyGoal: weeklyGoal || prev.weeklyGoal,
    }));
  };

  // Get surah completion percentage
  const getSurahCompletion = (surahNumber, totalAyahsInSurah) => {
    const surahKey = `${surahNumber}`;
    if (!progress.readSurahs[surahKey]) return 0;
    
    const ayahsRead = progress.readSurahs[surahKey].totalRead;
    return Math.min(100, Math.round((ayahsRead / totalAyahsInSurah) * 100));
  };

  // Get daily stats
  const getDailyStats = () => {
    const today = new Date().toDateString();
    let todayCount = 0;
    
    // Count ayahs read today
    Object.values(progress.readAyahs).forEach(timestamp => {
      if (new Date(timestamp).toDateString() === today) {
        todayCount++;
      }
    });
    
    return {
      ayahsReadToday: todayCount,
      dailyGoalProgress: Math.min(100, Math.round((todayCount / progress.dailyGoal) * 100)),
      currentStreak: progress.currentStreak,
    };
  };

  return (
    <QuranProgressContext.Provider value={{
      progress,
      markAyahAsRead,
      markAyahsAsRead,
      isAyahRead,
      resetProgress,
      updateGoals,
      getSurahCompletion,
      getDailyStats
    }}>
      {children}
    </QuranProgressContext.Provider>
  );
};

export const useQuranProgress = () => {
  const context = useContext(QuranProgressContext);
  if (!context) {
    throw new Error('useQuranProgress must be used within a QuranProgressProvider');
  }
  return context;
};

export default QuranProgressContext;