// Helper CSS that defines all the tajweed classes with their respective colors
const tajweedCSS = `
.ham_wasl { color: #AAAAAA !important; }
.slnt { color: #AAAAAA !important; }
.madda_normal { color: #537FFF !important; }
.madda_permissible { color: #4050FF !important; }
.madda_necessary { color: #000EBC !important; }
.qlq { color: #DD0008 !important; }
.madda_obligatory { color: #2144C1 !important; }
.ikhf_shfw { color: #D500B7 !important; }
.ikhf { color: #9400A8 !important; }
.idghm_shfw { color: #58B800 !important; }
.iqlb { color: #26BFFD !important; }
.idgh_ghn { color: #169777 !important; }
.idgh_w_ghn { color: #169200 !important; }
.idgh_mus { color: #A1A1A1 !important; }
.idgh_mut { color: #A1A1A1 !important; }
.ghn { color: #FF7E1E !important; }

/* Add cursor style to indicate hoverable tajweed elements */
[data-tajweed-rule] {
  cursor: help;
}
`;

// Add CSS to document if it's in a browser environment
if (typeof document !== 'undefined') {
  // Only add the styles if they haven't been added already
  if (!document.getElementById('tajweed-styles')) {
    const style = document.createElement('style');
    style.id = 'tajweed-styles';
    style.textContent = tajweedCSS;
    document.head.appendChild(style);
  }
}

/**
 * Parses tajweed using a method similar to the original PHP implementation.
 * @param {string} text - Verse text with tajweed markup.
 * @param {boolean} addDataAttributes - Whether to include data-* attributes (for tooltips).
 * @returns {string} - HTML string with <span> tags.
 */
function parseLikePHP(text, addDataAttributes = true) {
    let processed = text;

    // Step 1: Replace identifiers with the start of the span tag
    for (const metaKey in TAJWEED_META) {
        const meta = TAJWEED_META[metaKey];
        if (!meta || !meta.identifier) continue; // Skip if meta is invalid

        const identifier = meta.identifier;
        const cssClass = meta.default_css_class;
        const color = meta.html_color;

        // Construct the starting tag part, leaving it open (no '>')
        let startTag = `<span class="${cssClass}" style="color:${color};"`; // Use span instead of <tajweed>

        if (addDataAttributes) {
            const ruleTitle = meta.type.split('-').map(word => word.charAt(0).toUpperCase() + word.slice(1)).join(' ');
            const ruleDescription = meta.description;
            const ruleData = `${ruleTitle}:${ruleDescription}`;
            startTag += ` data-tajweed-rule="${ruleData}" data-tajweed-color="${color}"`;
        }
        // IMPORTANT: Still no closing '>'

        // Use replaceAll for global replacement of the identifier
        // Need to escape the identifier for replaceAll if it contains special regex chars (like '[')
        // Although, since we are replacing literal strings, escaping might not be strictly needed for replaceAll
        // Let's assume identifiers are simple like '[n' and don't need escaping for string replaceAll
        processed = processed.replaceAll(identifier, startTag);
    }

    // Step 2: Close the start tags and add end tags based on brackets
    // Replace the '[' that follows the content with the closing '>' for the span tag
    processed = processed.replaceAll('[', '">');
    // Replace the ']' that marks the end of the content with the closing '</span>' tag
    processed = processed.replaceAll(']', '</span>');

    // Step 3: Clean up any accidentally created empty tags or malformed structures
    // (This might be needed if the input format is inconsistent)
    // Example: Remove empty spans: <span ...></span>
    processed = processed.replace(/<span[^>]*><\/span>/g, '');

    return processed;
}

/**
 * Applies ZWJ fixes similar to the PHP webkitFix function.
 * @param {string} text - HTML string generated by parseLikePHP.
 * @returns {string} - HTML string with ZWJ characters inserted.
 */
function webkitFixJS(text) {
    const ZWJ = '\u200D'; // Unicode ZWJ character
    let fixed = text;

    // Regex to match closing span followed by a non-whitespace character (\S)
    // Inserts ZWJ between them
    fixed = fixed.replace(/(<\/span>)(\S)/g, `$1${ZWJ}$2`);

    // Regex to match a non-whitespace character (\S) followed by an opening span
    // Inserts ZWJ between them
    // This regex handles attributes within the span tag
    fixed = fixed.replace(/(\S)(<span\s+[^>]*>)/g, `$1${ZWJ}$2`);

    // --- Optional: ZWJ inside spans (less common, but might help) ---
    // fixed = fixed.replace(/(<span\s+[^>]*>)(\S)/g, `$1${ZWJ}$2`); // After span start
    // fixed = fixed.replace(/(\S)(<\/span>)/g, `$1${ZWJ}$2`); // Before span end
    // --- End Optional ---

    // Specific Alif fix (mimicking PHP) - if '' is U+0671
     fixed = fixed.replace(new RegExp('\u0671' + ZWJ, 'g'), '\u0671');
     // Add other non-joining characters if needed (e.g., , , , , , )
     // Example for '' (U+0627):
     // fixed = fixed.replace(new RegExp('\u0627' + ZWJ, 'g'), '\u0627');


    // Cleanup redundant ZWJ and ZWJ near spaces
    fixed = fixed.replace(new RegExp(ZWJ + ZWJ, 'g'), ZWJ); // Replace double ZWJ with single
    fixed = fixed.replace(new RegExp('\\s' + ZWJ, 'g'), ' '); // Remove ZWJ after space
    fixed = fixed.replace(new RegExp(ZWJ + '\\s', 'g'), ' '); // Remove ZWJ before space

    return fixed;
}

/**
 * Main parsing function - Orchestrator.
 * @param {string} text - Verse text with tajweed markup
 * @param {boolean} applyColors - Whether to apply color styling (defaults to true)
 * @param {boolean} forceMobileFix - Force the mobile-specific rendering logic
 * @returns {string} - HTML formatted text with tajweed markup
 */
export function parseTajweedText(text, applyColors = true, forceMobileFix = false) {
  if (!text) {
    return '';
  }

  // Determine if we need the mobile-specific fix
  const isMobileWebKit = forceMobileFix || (/iPhone|iPad|iPod/i.test(navigator?.userAgent) || (/Android/i.test(navigator?.userAgent) && /AppleWebKit/i.test(navigator?.userAgent) && !/Chrome/i.test(navigator?.userAgent)));

  if (!applyColors) {
    // Simple stripping if colors are off (remains the same)
    let strippedText = text.replace(/\[([a-z])(?::\d+)?\[([^\]]*)\]/g, '$2');
    strippedText = strippedText.replace(/[\[\]]/g, ''); // Clean up any stray brackets
    return strippedText;
  }

  // Generate the base HTML using the PHP-like logic
  // ALWAYS add data attributes now
  const addDataAttrs = true; // Changed from !isMobileWebKit
  let htmlResult = parseLikePHP(text, addDataAttrs);

  // Apply the ZWJ fix only for mobile WebKit (still needed for structure)
  if (isMobileWebKit) {
    htmlResult = webkitFixJS(htmlResult);
  }

  // Return the result
  return htmlResult;
}

// Export the main function
export default parseTajweedText;
