const QuranPageContent = () => {
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('md'));
  const { settings, updateSetting } = useQuranSettings();
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [quranData, setQuranData] = useState(null);
  const [translationData, setTranslationData] = useState(null);
  const [tafsirData, setTafsirData] = useState(null);
  const [tajweedQuranData, setTajweedQuranData] = useState(null);
  const [selectedSurah, setSelectedSurah] = useState(1); // Default to Al-Fatiha
  const [showSettings, setShowSettings] = useState(false);
  const [audioPlaying, setAudioPlaying] = useState(false);
  const [currentAyah, setCurrentAyah] = useState(null);
  const [bookmarkedAyahs, setBookmarkedAyahs] = useState([]);
  const [navigationMode, setNavigationMode] = useState(settings.navigationMode || NAVIGATION_MODES.PAGE);
  const [currentPosition, setCurrentPosition] = useState(1); // Default position in current navigation mode
  const [showNavigationPanel, setShowNavigationPanel] = useState(false);
  const [displayedAyahs, setDisplayedAyahs] = useState([]);
  const [selectedWord, setSelectedWord] = useState(null); // { surahNumber, ayahNumber, wordNumberInAyah }
  const [showAudioControls, setShowAudioControls] = useState(false);
  const [playbackRate, setPlaybackRate] = useState(1);
  const [repeatAyah, setRepeatAyah] = useState(false);

  const audioRef = useRef(null);

  // Show Safari Tajweed warning if user is on Safari and wants Tajweed
  const showSafariTajweedWarning = isSafari() && settings.quranEdition === 'quran-tajweed';

  // Load selected Quran edition
  useEffect(() => {
    async function loadQuranEdition() {
      try {
        setLoading(true);
        // Use the correct edition identifier from settings
        const quran = await quranService.loadEdition(settings.quranEdition);
        setQuranData(quran);
        setError(null);
      } catch (err) {
        console.error('Error loading Quran:', err);
        setError('Failed to load Quran data. Please try again.');
      } finally {
        setLoading(false);
      }
    }
    loadQuranEdition();
  }, [settings.quranEdition]);
  
  // Always load quran-tajweed in parallel
  useEffect(() => {
    async function loadTajweedQuran() {
      try {
        const tajweedQuran = await quranService.loadEdition('quran-tajweed');
        setTajweedQuranData(tajweedQuran);
      } catch (err) {
        console.error('Error loading Tajweed Quran:', err);
        setTajweedQuranData(null);
      }
    }
    loadTajweedQuran();
  }, []);
  
  // Load translation if enabled
  useEffect(() => {
    async function loadTranslation() {
      if (!settings.showTranslation) {
        setTranslationData(null);
        return;
      }
      
      try {
        const translation = await quranService.loadEdition(settings.translationId);
        setTranslationData(translation);
      } catch (err) {
        console.error('Error loading translation:', err);
        setTranslationData(null);
      }
    }
    
    loadTranslation();
  }, [settings.showTranslation, settings.translationId]);
  
  // Load tafsir if enabled
  useEffect(() => {
    async function loadTafsir() {
      if (!settings.showTafsir) {
        setTafsirData(null);
        return;
      }
      
      try {
        const tafsir = await quranService.loadEdition(settings.tafsirId);
        setTafsirData(tafsir);
      } catch (err) {
        console.error('Error loading tafsir:', err);
        setTafsirData(null);
      }
    }
    
    loadTafsir();
  }, [settings.showTafsir, settings.tafsirId]);

  // Debug: print current edition settings
  useEffect(() => {
    console.log('Current Quran Edition:', settings.quranEdition);
    console.log('Current Translation Edition:', settings.translationId);
    console.log('Current Tafsir Edition:', settings.tafsirId);
  }, [settings.quranEdition, settings.translationId, settings.tafsirId]);

  // Load bookmarks from localStorage
  useEffect(() => {
    const savedBookmarks = localStorage.getItem('quranBookmarks');
    if (savedBookmarks) {
      try {
        setBookmarkedAyahs(JSON.parse(savedBookmarks));
      } catch (error) {
        console.error('Error loading bookmarks:', error);
      }
    }
  }, []);

  // Update navigation mode when settings change
  useEffect(() => {
    setNavigationMode(settings.navigationMode || NAVIGATION_MODES.PAGE);
  }, [settings.navigationMode]);
  
  // Update displayed ayahs based on navigation mode and position
  useEffect(() => {
    if (!quranData || !navigationMode) return;

    // Surah mode: just display the selected surah
    if (navigationMode === NAVIGATION_MODES.SURAH) {
      // For surah mode, we'll use the existing code that displays the entire surah
      return;
    }

    // For other navigation modes, filter ayahs by the selected marker
    const filteredAyahs = [];
    
    // Get the actual property name from the mapping
    const propertyName = PROPERTY_MAPPINGS[navigationMode] || navigationMode;
    
    // Special handling for hizb quarters
    let position = currentPosition;
    if (navigationMode === NAVIGATION_MODES.HIZB) {
      // Convert hizb number (1-8) to hizbQuarter (1-60) range
      position = Math.ceil(currentPosition / 8 * 60);
    }

    // Get debugging info
    console.log(`Filtering by ${propertyName} = ${position}`);
    
    quranData.surahs.forEach(surah => {
      surah.ayahs.forEach(ayah => {
        // Check if this ayah matches the current position in the selected navigation mode
        if (ayah[propertyName] === position) {
          filteredAyahs.push({
            ...ayah,
            surahNumber: surah.number,
            surahName: surah.name,
            surahEnglishName: surah.englishName,
          });
        }
      });
    });
    
    // Sort ayahs by their position in the Quran
    filteredAyahs.sort((a, b) => a.number - b.number);
    
    setDisplayedAyahs(filteredAyahs);
    
    // If we found matching ayahs, update the selected surah to the first one
    if (filteredAyahs.length > 0) {
      setSelectedSurah(filteredAyahs[0].surahNumber);
    }
  }, [quranData, navigationMode, currentPosition]);
  
  // Handle audio playback
  useEffect(() => {
    if (!audioRef.current || !currentAyah || !quranData) return;
    
    const handleAudioEnd = () => {
      if (settings.autoPlayAyahs) {
        // Use our new helper method to get the next ayah within current navigation boundaries
        const propertyName = PROPERTY_MAPPINGS[navigationMode] || navigationMode;
        let nextAyah = null;
        
        if (navigationMode === NAVIGATION_MODES.SURAH) {
          // In surah mode, simply get the next ayah in the current surah
          const nextAyahNumber = currentAyah.numberInSurah + 1;
          const surah = quranData.getSurah(selectedSurah);
          if (surah && nextAyahNumber <= surah.ayahs.length) {
            nextAyah = surah.getAyah(nextAyahNumber);
          }
        } else {
          // For other modes, find the next ayah in the displayed ayahs
          const currentIndex = displayedAyahs.findIndex(a => 
            a.number === currentAyah.number || 
            (a.numberInSurah === currentAyah.numberInSurah && a.surahNumber === currentAyah.surahNumber)
          );
          
          if (currentIndex >= 0 && currentIndex < displayedAyahs.length - 1) {
            nextAyah = displayedAyahs[currentIndex + 1];
          }
        }
        
        if (nextAyah) {
          setCurrentAyah(nextAyah);
          playAyah(nextAyah);
        } else {
          setAudioPlaying(false);
        }
      } else {
        setAudioPlaying(false);
      }
    };
    
    audioRef.current.addEventListener('ended', handleAudioEnd);
    
    return () => {
      if (audioRef.current) {
        audioRef.current.removeEventListener('ended', handleAudioEnd);
      }
    };
  }, [currentAyah, settings.autoPlayAyahs, quranData, selectedSurah, navigationMode, displayedAyahs]);

  // Update playback rate when changed
  useEffect(() => {
    if (audioRef.current) {
      audioRef.current.playbackRate = playbackRate;
    }
  }, [playbackRate]);

  // Repeat ayah logic
  useEffect(() => {
    if (!audioRef.current || !currentAyah) return;
    const handleAudioEnd = () => {
      if (repeatAyah) {
        playAyah(currentAyah);
      }
    };
    audioRef.current.addEventListener('ended', handleAudioEnd);
    return () => {
      if (audioRef.current) {
        audioRef.current.removeEventListener('ended', handleAudioEnd);
      }
    };
  }, [repeatAyah, currentAyah]);
  
  const playAyah = (ayah) => {
    if (!audioRef.current || !ayah) return;
    
    // Get audio URL using the ayah object directly to use its global number
    const audioUrl = quranService.getAudioUrl(
      ayah.surahNumber || selectedSurah, 
      ayah, 
      settings.reciterId,
      settings.audioBitrate || 128 // Use the bitrate from settings or default to 128
    );
    
    // First ensure the UI updates to show which ayah is active
    setCurrentAyah(ayah);
    
    // Small delay before playing audio to allow UI to update
    setTimeout(() => {
      audioRef.current.src = audioUrl;
      audioRef.current.play()
        .then(() => {
          setAudioPlaying(true);
          
          // After a short delay, scroll to the ayah if it's not in view
          setTimeout(() => {
            const ayahElement = document.getElementById(`ayah-${ayah.surahNumber || selectedSurah}-${ayah.numberInSurah}`);
            if (ayahElement) {
              // Check if element is in viewport
              const rect = ayahElement.getBoundingClientRect();
              const isInViewport = (
                rect.top >= 0 &&
                rect.left >= 0 &&
                rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
                rect.right <= (window.innerWidth || document.documentElement.clientWidth)
              );
              
              // Only scroll if not in viewport
              if (!isInViewport) {
                ayahElement.scrollIntoView({ 
                  behavior: 'smooth', 
                  block: 'center' 
                });
              }
            }
          }, 300);
        })
        .catch(err => {
          console.error('Error playing audio:', err);
          setAudioPlaying(false);
          
          // If audio fails to play, try a lower bitrate
          if ((settings.audioBitrate || 128) > 64) {
            console.log('Trying lower bitrate...');
            const lowerBitrateUrl = quranService.getAudioUrl(
              ayah.surahNumber || selectedSurah,
              ayah,
              settings.reciterId,
              64
            );
            audioRef.current.src = lowerBitrateUrl;
            audioRef.current.play()
              .then(() => setAudioPlaying(true))
              .catch(err => {
                console.error('Error playing audio with lower bitrate:', err);
                setAudioPlaying(false);
              });
          }
        });
    }, 100);
  };
  
  const pauseAudio = () => {
    if (audioRef.current) {
      audioRef.current.pause();
      setAudioPlaying(false);
    }
  };
  
  const handleAyahClick = (ayah) => {
    setCurrentAyah(ayah);
    if (audioPlaying) {
      pauseAudio();
    }
  };
  
  const handlePlayPause = () => {
    if (audioPlaying) {
      pauseAudio();
    } else if (currentAyah) {
      playAyah(currentAyah);
    } else {
      // Play first ayah if none selected
      const surah = quranData?.getSurah(selectedSurah);
      if (surah && surah.ayahs.length > 0) {
        const firstAyah = surah.getAyah(1);
        setCurrentAyah(firstAyah);
        playAyah(firstAyah);
      }
    }
  };
  
  const handleSurahSelect = (surahNumber) => {
    setSelectedSurah(surahNumber);
    setCurrentAyah(null);
    if (audioPlaying) {
      pauseAudio();
    }
    
    // If we're selecting a surah directly, set navigation mode to surah
    if (navigationMode !== NAVIGATION_MODES.SURAH) {
      setNavigationMode(NAVIGATION_MODES.SURAH);
      updateSetting('navigationMode', NAVIGATION_MODES.SURAH);
      setCurrentPosition(surahNumber);
    }
  };
  
  const handleFontSizeChange = (increase) => {
    updateSetting('fontSize', Math.max(16, Math.min(36, settings.fontSize + (increase ? 2 : -2))));
  };

  const handleNavigationModeChange = (mode) => {
    setNavigationMode(mode);
    updateSetting('navigationMode', mode);
    setCurrentPosition(1); // Reset to first position in the new mode
  };

  const handlePositionChange = (position) => {
    setCurrentPosition(position);
    
    // For surah mode, set the selected surah directly
    if (navigationMode === NAVIGATION_MODES.SURAH) {
      setSelectedSurah(position);
    }
    // For other modes, the ayahs will be filtered in the useEffect
  };

  const toggleBookmark = (surahNumber, ayahNumber) => {
    const bookmarkId = `${surahNumber}:${ayahNumber}`;
    
    setBookmarkedAyahs(prevBookmarks => {
      let newBookmarks;
      
      if (prevBookmarks.includes(bookmarkId)) {
        newBookmarks = prevBookmarks.filter(id => id !== bookmarkId);
      } else {
        newBookmarks = [...prevBookmarks, bookmarkId];
      }
      
      // Save to localStorage
      localStorage.setItem('quranBookmarks', JSON.stringify(newBookmarks));
      
      return newBookmarks;
    });
  };

  const isAyahBookmarked = (surahNumber, ayahNumber) => {
    return bookmarkedAyahs.includes(`${surahNumber}:${ayahNumber}`);
  };

  const handleNavigate = (mode, position) => {
    setNavigationMode(mode);
    updateSetting('navigationMode', mode);
    handlePositionChange(position);
    setShowNavigationPanel(false);
  };

  const handleNextNavigation = () => {
    const maxPos = getMaxPositionByMode();
    if (currentPosition < maxPos) {
      handlePositionChange(currentPosition + 1);
      // Scroll to top after navigation
    window.scrollTo({ top: 0, behavior: 'smooth' });
    }
  };

  const handlePreviousNavigation = () => {
    if (currentPosition > 1) {
      handlePositionChange(currentPosition - 1);
    }
  };

  // Handle navigation from progress view
  const handleProgressNavigation = (mode, position, ayah = null) => {
    setNavigationMode(mode);
    updateSetting('navigationMode', mode);
    
    if (mode === NAVIGATION_MODES.SURAH) {
      setSelectedSurah(position);
      
      // If we have an ayah number, scroll to it (using setTimeout to ensure DOM is ready)
      if (ayah) {
        setTimeout(() => {
          const ayahElement = document.getElementById(`ayah-${position}-${ayah}`);
          if (ayahElement) {
            ayahElement.scrollIntoView({ behavior: 'smooth', block: 'start' });
          }
        }, 500);
      }
    } else {
      handlePositionChange(position);
    }
    
    setShowNavigationPanel(false);
  };

  // Group displayed ayahs by surah for non-surah navigation modes
  const getAyahsGroupedBySurah = () => {
    if (!displayedAyahs || displayedAyahs.length === 0) return [];
    
    const surahMap = new Map();
    
    displayedAyahs.forEach(ayah => {
      const surahNumber = ayah.surahNumber;
      
      if (!surahMap.has(surahNumber)) {
        surahMap.set(surahNumber, {
          number: surahNumber,
          name: ayah.surahName,
          englishName: ayah.surahEnglishName,
          ayahs: []
        });
      }
      
      surahMap.get(surahNumber).ayahs.push(ayah);
    });
    
    return Array.from(surahMap.values());
  };

  // Gets the maximum position for the current navigation mode
  const getMaxPositionByMode = () => {
    switch(navigationMode) {
      case NAVIGATION_MODES.PAGE:
        return 604; // Standard Quran has 604 pages
      case NAVIGATION_MODES.JUZ:
        return 30; // Quran has 30 juz
      case NAVIGATION_MODES.HIZB:
        return 8; // Quran has 8 hizbs (not 60 hizbQuarters)
      case NAVIGATION_MODES.RUKU:
        return 556; // Approximate number of rukus
      case NAVIGATION_MODES.MANZIL:
        return 7; // Quran has 7 manzils
      case NAVIGATION_MODES.SURAH:
        return 114; // Quran has 114 surahs
      default:
        return 114;
    }
  };

  // Get title for the current navigation mode
  const getNavigationTitle = () => {
    if (navigationMode === NAVIGATION_MODES.SURAH && selectedSurahData) {
      return `${selectedSurahData.name} - ${selectedSurahData.englishName}`;
    } else {
      const modeName = navigationMode === NAVIGATION_MODES.HIZB ? 'Hizb' : 
        navigationMode.charAt(0).toUpperCase() + navigationMode.slice(1);
      return `${modeName} ${currentPosition}`;
    }
  };

  const selectedSurahData = quranData?.getSurah(selectedSurah);
  const groupedAyahs = navigationMode !== NAVIGATION_MODES.SURAH ? getAyahsGroupedBySurah() : [];

  // --- SEO Meta Tag Generation ---
  const generateMetaTags = () => {
    let title = "Read & Listen to the Quran - iMuslim Life";
    let description = "Read and listen to the Holy Quran online with translations, tafsir, Tajweed highlighting, word-by-word analysis, and audio recitations.";
    const keywords = "quran, holy quran, read quran, listen quran, quran translation, quran tafsir, tajweed, quran audio, islamic scripture";
    const canonicalUrl = "https://imuslimlife.com/#/quran"; // Keep base canonical for now due to HashRouter

    try {
      if (navigationMode === NAVIGATION_MODES.SURAH && selectedSurahData) {
        title = `Read Surah ${selectedSurahData.englishName} (${selectedSurahData.englishNameTranslation}) - Quran | iMuslim Life`;
        description = `Read Surah ${selectedSurahData.number} (${selectedSurahData.englishName}) of the Holy Quran. Includes ${selectedSurahData.ayahs.length} ayahs. Explore with translation, tafsir, and audio recitation on iMuslim Life.`;
      } else if (navigationMode !== NAVIGATION_MODES.SURAH) {
        const modeName = navigationMode.charAt(0).toUpperCase() + navigationMode.slice(1);
        title = `Read Quran - ${modeName} ${currentPosition} | iMuslim Life`;
        description = `Explore the Holy Quran by ${modeName}. Currently viewing ${modeName} ${currentPosition}. Read with translation, tafsir, and listen to recitations on iMuslim Life.`;
      }
    } catch (e) {
      console.error("Error generating meta tags:", e);
      // Use default tags in case of error
    }

    return {
      title,
      description,
      keywords,
      canonicalUrl
    };
  };

  const meta = generateMetaTags();
  // --- End SEO Meta Tag Generation ---

  // --- Structured Data Generation ---
  const generateStructuredData = () => {
    // Only generate when in Surah mode and data is available
    if (navigationMode !== NAVIGATION_MODES.SURAH || !selectedSurahData) {
      return null;
    }

    // TODO: Get actual translator name based on settings.translationId
    const translatorName = quranService.getEditionDetails(settings.translationId)?.translator || "Selected Translator";

    const chapterSchema = {
      "@context": "https://schema.org",
      "@type": "Chapter",
      "@id": `https://imuslimlife.com/#/quran?surah=${selectedSurahData.number}`, // Add @id for referencing
      "isPartOf": {
        "@type": "Book",
        "name": "The Holy Quran",
        "author": {
          "@type": "Organization", // Using Organization as divine authorship is complex for schema
          "name": "Allah (God)"
        },
        "inLanguage": "ar" // Arabic
      },
      "name": `Surah ${selectedSurahData.englishName}`,
      "alternateName": selectedSurahData.name, // Arabic name
      "chapterNumber": selectedSurahData.number,
      "numberOfPages": selectedSurahData.ayahs.length, // Using number of verses as a proxy for 'pages' in this context
      "description": `Surah ${selectedSurahData.englishName} (${selectedSurahData.englishNameTranslation}), chapter ${selectedSurahData.number} of the Holy Quran. Revealed in ${selectedSurahData.revelationType}. Contains ${selectedSurahData.ayahs.length} verses (ayahs).`,
      "inLanguage": "ar", // Primary language of the chapter text
      "translator": settings.showTranslation ? {
        "@type": "Person", // Or Organization depending on the source
        "name": translatorName
      } : undefined, // Only include translator if translation is shown
      "url": `https://imuslimlife.com/#/quran?surah=${selectedSurahData.number}` // Current URL structure
    };

    return JSON.stringify(chapterSchema);
  };

  const structuredDataJson = useMemo(generateStructuredData, [selectedSurahData, navigationMode, settings.showTranslation, settings.translationId]);
  // --- End Structured Data Generation ---

  // --- Structured Data Generation (AudioObject) ---
  const generateAudioObjectSchema = () => {
    if (!currentAyah || !selectedSurahData) {
      return null;
    }

    const reciterInfo = reciters.find(r => r.value === settings.reciterId);
    const audioUrl = quranService.getAudioUrl(
      currentAyah.surahNumber || selectedSurah,
      currentAyah,
      settings.reciterId,
      settings.audioBitrate || 128
    );
    const surahUrl = `https://imuslimlife.com/#/quran?surah=${selectedSurahData.number}`;
    const ayahNumberInSurah = currentAyah.numberInSurah;

    const audioSchema = {
      "@context": "https://schema.org",
      "@type": "AudioObject",
      "name": `Recitation of Surah ${selectedSurahData.englishName}, Ayah ${ayahNumberInSurah}${reciterInfo ? ` by ${reciterInfo.label}` : ''}`,
      "description": `Audio recitation of verse ${ayahNumberInSurah} from Surah ${selectedSurahData.englishName} (${selectedSurahData.name}) of the Holy Quran. Recited by ${reciterInfo ? reciterInfo.label : 'selected reciter'}.`,
      "contentUrl": audioUrl,
      "encodingFormat": "audio/mpeg", // Assuming mp3 format from quranService
      "inLanguage": "ar", // Language of the recitation
      "byArtist": reciterInfo ? {
        "@type": "Person",
        "name": reciterInfo.label
      } : undefined,
      "isPartOf": {
        "@id": surahUrl // Reference the Chapter schema by its @id
      }
    };

    return JSON.stringify(audioSchema);
  };

  const audioSchemaJson = useMemo(generateAudioObjectSchema, [currentAyah, selectedSurahData, settings.reciterId, settings.audioBitrate, selectedSurah]);
  // --- End Structured Data Generation (AudioObject) ---

  if (loading) {
    return (
      <Container maxWidth="lg" sx={{ py: 4, textAlign: 'center' }}>
        <CircularProgress size={60} />
        <Typography variant="h6" sx={{ mt: 2 }}>
          Loading Quran...
        </Typography>
      </Container>
    );
  }

  if (error) {
    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Alert severity="error" sx={{ mb: 3 }}>
          {error}
        </Alert>
      </Container>
    );
  }

  return (
    <QuranProgressProvider>
      <Helmet>
        {/* Use dynamic title and description */}
        <title>{meta.title}</title>
        <meta name="description" content={meta.description} />
        <meta name="keywords" content={meta.keywords} />
        {/* Keep OG tags potentially dynamic or more general */}
        <meta property="og:title" content={meta.title} />
        <meta property="og:description" content={meta.description} />
        <link rel="canonical" href={meta.canonicalUrl} />
        {/* Add JSON-LD Structured Data (Chapter) */}
        {structuredDataJson && (
          <script type="application/ld+json">
            {structuredDataJson}
          </script>
        )}
        {/* Add JSON-LD Structured Data (AudioObject) */}
        {audioSchemaJson && (
          <script type="application/ld+json">
            {audioSchemaJson}
          </script>
        )}
      </Helmet>
      <Container maxWidth="xl" sx={{ py: 4 }}>
        {showSafariTajweedWarning && (
          <Alert severity="info" sx={{ mb: 3 }}>
            Tajweed Quran text displays best in Chrome or other browsers. On Safari, Tajweed rendering may not appear correctly. For the best Tajweed experience, please use our App "Salat-e-Mustaqeem" or switch to a supported browser.
          </Alert>
        )}
        <audio ref={audioRef} style={{ display: 'none' }} />
        
        {/* Add TajweedTooltip component here so it's available throughout the page */}
        <TajweedTooltip />
        
        {/* Quran Reading Progress Dashboard */}
        <QuranProgressView onNavigate={handleProgressNavigation} />
        
        {/* Header */}
        <Paper elevation={3} sx={{ p: 3, mb: 3, borderRadius: 2 }}>
          <Grid container spacing={2} alignItems="center">
            <Grid item xs={12} md={6}>
              <Typography variant="h4" component="h1">
                Al-Quran Al-Kareem
              </Typography>
              <Typography variant="subtitle1" color="text.secondary">
                {getNavigationTitle()}
              </Typography>
            </Grid>
            
            <Grid item xs={12} md={6} sx={{ display: 'flex', justifyContent: 'flex-end', gap: 1 }}>
              {/* Audio controls toggle */}
              <Tooltip title={showAudioControls ? 'Hide Audio Controls' : 'Show Audio Controls'}>
                <IconButton 
                  onClick={() => setShowAudioControls(v => !v)}
                  aria-label={showAudioControls ? 'Hide audio controls' : 'Show audio controls'}
                  aria-pressed={showAudioControls}
                >
                  <MusicNoteIcon color={showAudioControls ? 'primary' : 'inherit'} />
                </IconButton>
              </Tooltip>
              <Tooltip title="Navigation">
                <IconButton 
                  onClick={() => setShowNavigationPanel(true)}
                  aria-label="Open Quran navigation panel"
                >
                  <MenuIcon />
                </IconButton>
              </Tooltip>

              <Tooltip title="Decrease font size">
                <IconButton 
                  onClick={() => handleFontSizeChange(false)}
                  aria-label="Decrease font size"
                  disabled={settings.fontSize <= 16}
                >
                  <ZoomOutIcon />
                </IconButton>
              </Tooltip>
              
              <Tooltip title="Increase font size">
                <IconButton 
                  onClick={() => handleFontSizeChange(true)}
                  aria-label="Increase font size"
                  disabled={settings.fontSize >= 36}
                >
                  <ZoomInIcon />
                </IconButton>
              </Tooltip>
              
              <Tooltip title={audioPlaying ? "Pause recitation" : "Play recitation"}>
                <IconButton 
                  onClick={handlePlayPause}
                  color={audioPlaying ? "primary" : "default"}
                  aria-label={audioPlaying ? "Pause recitation" : "Play recitation"}
                  aria-pressed={audioPlaying}
                >
                  {audioPlaying ? <PauseIcon /> : <PlayArrowIcon />}
                </IconButton>
              </Tooltip>
              
              <Tooltip title="Settings">
                <IconButton 
                  onClick={() => setShowSettings(true)}
                  aria-label="Open Quran settings panel"
                >
                  <SettingsIcon />
                </IconButton>
              </Tooltip>
            </Grid>
          </Grid>
          
          {/* Audio Controls Row */}
          {showAudioControls && (
            <Box sx={{ mt: 2, display: 'flex', flexWrap: 'wrap', alignItems: 'center', gap: 2 }}>
              {/* Playback Speed */}
              <FormControl size="small" sx={{ minWidth: 120 }}>
                <InputLabel id="playback-rate-label">Speed</InputLabel>
                <Select
                  labelId="playback-rate-label"
                  value={playbackRate}
                  label="Speed"
                  onChange={e => setPlaybackRate(Number(e.target.value))}
                >
                  <MenuItem value={0.5}>0.5x</MenuItem>
                  <MenuItem value={1}>1x</MenuItem>
                  <MenuItem value={1.5}>1.5x</MenuItem>
                  <MenuItem value={2}>2x</MenuItem>
                </Select>
              </FormControl>
              {/* Repeat toggle */}
              <Tooltip title={repeatAyah ? 'Stop repeating current Ayah' : 'Repeat current Ayah'}>
                <IconButton 
                  onClick={() => setRepeatAyah(v => !v)} 
                  color={repeatAyah ? 'primary' : 'default'}
                  aria-label={repeatAyah ? 'Stop repeating current Ayah' : 'Repeat current Ayah'}
                  aria-pressed={repeatAyah}
                >
                  {repeatAyah ? <RepeatOneIcon /> : <RepeatIcon />}
                </IconButton>
              </Tooltip>
              {/* Reciter select */}
              <FormControl size="small" sx={{ minWidth: 180 }}>
                <InputLabel id="reciter-select-label">Reciter</InputLabel>
                <Select
                  labelId="reciter-select-label"
                  value={settings.reciterId}
                  label="Reciter"
                  onChange={e => updateSetting('reciterId', e.target.value)}
                >
                  {reciters.map(r => (
                    <MenuItem key={r.value} value={r.value}>
                      <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                        {r.image && (
                          <img
                            src={r.image}
                            alt={r.label}
                            style={{ width: 28, height: 28, borderRadius: '50%', objectFit: 'cover' }}
                            loading="lazy"
                          />
                        )}
                        <span>{r.label}</span>
                      </Box>
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Box>
          )}
          
          {/* Navigation Controls */}
          <Box sx={{ mt: 3, pt: 2, borderTop: `1px solid ${theme.palette.divider}` }}>
            <QuranNavigationControls
              navigationMode={navigationMode}
              currentPosition={currentPosition}
              maxPosition={getMaxPositionByMode()}
              onNavigationModeChange={handleNavigationModeChange}
              onPositionChange={handlePositionChange}
              onGoToSurah={() => setShowNavigationPanel(true)}
            />
          </Box>
        </Paper>
        
        {/* Main content */}
        <Grid container spacing={3}>
          {/* Surah list sidebar on larger screens - Only shown in surah mode */}
          {!isSmallScreen && navigationMode === NAVIGATION_MODES.SURAH && (
            <Grid item xs={12} md={3}>
              <SurahList 
                surahs={quranData?.surahs || []}
                selectedSurah={selectedSurah}
                onSelectSurah={handleSurahSelect}
              />
            </Grid>
          )}
          
          {/* Main Quran display */}
          <Grid item xs={12} md={navigationMode === NAVIGATION_MODES.SURAH && !isSmallScreen ? 9 : 12}>
            {/* Surah Mode Display */}
            {navigationMode === NAVIGATION_MODES.SURAH && selectedSurahData ? (
              <Paper 
                elevation={3} 
                sx={{ 
                  p: 3, 
                  borderRadius: 2,
                  backgroundColor: theme.palette.background.default
                }}
              >
                {/* Surah header */}
                <Box sx={{ textAlign: 'center', mb: 2 }}>
                  <Typography 
                    variant="h4" // Keep visual style, but change semantic tag
                    component="h2" // Change to H2
                    sx={{ 
                      fontFamily: settings.fontFamily,
                      fontSize: settings.fontSize * 1.2,
                      fontWeight: 'bold',
                      mb: 1
                    }}
                  >
                    {selectedSurahData.name}
                  </Typography>
                  
                  <Typography 
                    variant="h5" // Keep visual style, but change semantic tag
                    component="h3" // Change to H3
                  >
                    {selectedSurahData.englishName} - {selectedSurahData.englishNameTranslation}
                  </Typography>
                  
                  <Typography variant="body2" color="text.secondary">
                    {selectedSurahData.revelationType}  {selectedSurahData.ayahs.length} Ayahs
                  </Typography>
                  
                  {/* Bismillah except for Surah At-Tawba */}
                  {selectedSurahData.hasBismillah && selectedSurahData.hasBismillah() && (
                    <Box 
                      sx={{ 
                        my: 3, 
                        py: 2,
                        borderTop: `1px solid ${theme.palette.divider}`,
                        borderBottom: `1px solid ${theme.palette.divider}`,
                      }}
                    >
                      <Typography 
                        variant="h5" 
                        sx={{ 
                          fontFamily: settings.fontFamily,
                          fontSize: settings.fontSize * 1.1,
                          textAlign: 'center',
                          direction: 'rtl'
                        }}
                        dangerouslySetInnerHTML={{
                          __html: parseTajweedText(
                            " [h:1[] [h:2[][l[][n[] [h:3[][l[][p[]",
                            settings.showTajweed
                          )
                        }}
                      />
                    </Box>
                  )}
                </Box>
                
                {/* Ayahs */}
                <Box>
                  {selectedSurahData.ayahs.map((ayah) => (
                    <EnhancedAyahView 
                      key={ayah.numberInSurah}
                      ayah={ayah}
                      surah={selectedSurahData}
                      settings={settings}
                      translation={settings.showTranslation ? 
                        translationData?.getSurah(selectedSurah)?.getAyah(ayah.numberInSurah) : null
                      }
                      tafsir={settings.showTafsir ? 
                        tafsirData?.getSurah(selectedSurah)?.getAyah(ayah.numberInSurah) : null
                      }
                      tajweedQuranData={settings.quranEdition === 'quran-wordbyword-2' ? tajweedQuranData : undefined}
                      isPlaying={audioPlaying && currentAyah?.number === ayah.number}
                      isBookmarked={isAyahBookmarked(selectedSurah, ayah.numberInSurah)}
                      onPlay={() => {
                        setCurrentAyah(ayah);
                        playAyah(ayah);
                      }}
                      onPause={pauseAudio}
                      onToggleBookmark={() => toggleBookmark(selectedSurah, ayah.numberInSurah)}
                      onClick={() => handleAyahClick(ayah)}
                      selectedWord={selectedWord}
                      setSelectedWord={setSelectedWord}
                    />
                  ))}
                </Box>
                
                {/* Navigation */}
                <Box sx={{ 
                  display: 'flex', 
                  justifyContent: 'space-between',
                  mt: 3, 
                  pt: 2,
                  borderTop: `1px solid ${theme.palette.divider}`,
                }}>
                  <Button
                    variant="outlined"
                    startIcon={<NavigateBeforeIcon />}
                    onClick={handlePreviousNavigation}
                    disabled={currentPosition <= 1}
                  >
                    Previous Surah
                  </Button>
                  
                  <Button
                    variant="outlined"
                    endIcon={<NavigateNextIcon />}
                    onClick={handleNextNavigation}
                    disabled={currentPosition >= getMaxPositionByMode()}
                  >
                    Next Surah
                  </Button>
                </Box>
              </Paper>
            ) : navigationMode !== NAVIGATION_MODES.SURAH && groupedAyahs.length > 0 ? (
              // Other Navigation Modes (page, juz, hizb, ruku, manzil)
              <Paper 
                elevation={3} 
                sx={{ 
                  p: 3, 
                  borderRadius: 2,
                  backgroundColor: theme.palette.background.default
                }}
              >
                {/* Navigation header */}
                <Box sx={{ textAlign: 'center', mb: 3 }}>
                  <Typography 
                    variant="h4" // Keep visual style, but change semantic tag
                    component="h2" // Change to H2
                    sx={{ fontFamily: settings.fontFamily, mb: 1 }}
                  >
                    {navigationMode.charAt(0).toUpperCase() + navigationMode.slice(1)} {currentPosition}
                  </Typography>
                  
                  <Typography variant="body2" color="text.secondary">
                    {displayedAyahs.length} Ayahs from {groupedAyahs.length} Surahs
                  </Typography>
                </Box>
                
                {/* Display ayahs grouped by surah */}
                {groupedAyahs.map((group, index) => (
                  <Box key={`group-${group.number}`} sx={{ mb: 4 }}>
                    {/* Surah header for each group */}
                    <Box sx={{ 
                      textAlign: 'center', 
                      mb: 2,
                      pb: 1,
                      borderBottom: `1px solid ${theme.palette.divider}`
                    }}>
                      <Typography 
                        variant="h6" 
                        sx={{ 
                          fontFamily: settings.fontFamily,
                          fontWeight: 'bold',
                          mb: 0.5
                        }}
                      >
                        {group.name}
                      </Typography>
                      
                      <Typography variant="body2">
                        {group.englishName}
                      </Typography>
                    </Box>
                    
                    {/* Show Bismillah for each new surah except for At-Tawba (surah 9) */}
                    {group.number !== 9 && (
                      <Box sx={{ 
                        my: 2, 
                        py: 1,
                        textAlign: 'center' 
                      }}>
                        <Typography 
                          variant="h6" 
                          sx={{ 
                            fontFamily: settings.fontFamily,
                            fontSize: settings.fontSize * 1.1,
                            direction: 'rtl'
                          }}
                          dangerouslySetInnerHTML={{
                            __html: parseTajweedText(
                              " [h:1[] [h:2[][l[][n[] [h:3[][l[][p[]",
                              settings.showTajweed
                            )
                          }}
                        />
                      </Box>
                    )}
                    
                    {/* Ayahs in this group */}
                    {group.ayahs.map((ayah) => {
                      const surahObj = quranData?.getSurah(group.number);
                      return (
                        <EnhancedAyahView 
                          key={`${group.number}-${ayah.numberInSurah}`}
                          ayah={ayah}
                          surah={surahObj}
                          settings={settings}
                          translation={settings.showTranslation ? 
                            translationData?.getSurah(group.number)?.getAyah(ayah.numberInSurah) : null
                          }
                          tafsir={settings.showTafsir ? 
                            tafsirData?.getSurah(group.number)?.getAyah(ayah.numberInSurah) : null
                          }
                          tajweedQuranData={settings.quranEdition === 'quran-wordbyword-2' ? tajweedQuranData : undefined}
                          isPlaying={audioPlaying && currentAyah?.number === ayah.number}
                          isBookmarked={isAyahBookmarked(group.number, ayah.numberInSurah)}
                          onPlay={() => {
                            setCurrentAyah(ayah);
                            playAyah(ayah);
                          }}
                          onPause={pauseAudio}
                          onToggleBookmark={() => toggleBookmark(group.number, ayah.numberInSurah)}
                          onClick={() => handleAyahClick(ayah)}
                          selectedWord={selectedWord}
                          setSelectedWord={setSelectedWord}
                        />
                      );
                    })}
                  </Box>
                ))}
                
                {/* Navigation buttons */}
                <Box sx={{ 
                  display: 'flex', 
                  justifyContent: 'space-between',
                  mt: 3, 
                  pt: 2,
                  borderTop: `1px solid ${theme.palette.divider}`,
                }}>
                  <Button
                    variant="outlined"
                    startIcon={<NavigateBeforeIcon />}
                    onClick={handlePreviousNavigation}
                    disabled={currentPosition <= 1}
                  >
                    Previous {navigationMode.charAt(0).toUpperCase() + navigationMode.slice(1)}
                  </Button>
                  
                  <Button
                    variant="outlined"
                    endIcon={<NavigateNextIcon />}
                    onClick={handleNextNavigation}
                    disabled={currentPosition >= getMaxPositionByMode()}
                  >
                    Next {navigationMode.charAt(0).toUpperCase() + navigationMode.slice(1)}
                  </Button>
                </Box>
              </Paper>
            ) : (
              // Fallback for empty state
              <Paper 
                elevation={3} 
                sx={{ 
                  p: 3, 
                  borderRadius: 2,
                  textAlign: 'center'
                }}
              >
                <Typography 
                  variant="h5" 
                  component="h2" // Use H2 for the fallback title as well
                >
                  No content found
                </Typography>
                <Typography variant="body1" sx={{ mt: 2 }}>
                  {navigationMode === NAVIGATION_MODES.SURAH ? 
                    'Select a Surah to begin' : 
                    `No ayahs found for ${navigationMode} ${currentPosition}. Try a different ${navigationMode} or navigation mode.`
                  }
                </Typography>
                
                {/* On small screens show surah select */}
                {isSmallScreen && navigationMode === NAVIGATION_MODES.SURAH && (
                  <FormControl fullWidth sx={{ mt: 2 }}>
                    <InputLabel id="surah-select-label">Surah</InputLabel>
                    <Select
                      labelId="surah-select-label"
                      value={selectedSurah}
                      onChange={(e) => handleSurahSelect(e.target.value)}
                      label="Surah"
                    >
                      {quranData?.surahs.map(surah => (
                        <MenuItem key={surah.number} value={surah.number}>
                          {surah.number}. {surah.englishName}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                )}
              </Paper>
            )}
          </Grid>
        </Grid>
        
        {/* Settings Panel */}
        <QuranSettingsPanel
          open={showSettings}
          onClose={() => setShowSettings(false)}
        />

        {/* Navigation Panel Dialog */}
        <Dialog 
          open={showNavigationPanel}
          onClose={() => setShowNavigationPanel(false)}
          maxWidth="md"
          fullWidth
        >
          <DialogTitle sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            Quran Navigation
            <IconButton onClick={() => setShowNavigationPanel(false)} edge="end">
              <CloseIcon />
            </IconButton>
          </DialogTitle>
          <DialogContent>
            <QuranNavigationPanel onNavigate={handleNavigate} />
          </DialogContent>
        </Dialog>
        
        {/* Attribution Footer */}
        <Paper 
          elevation={1} 
          sx={{ 
            p: 2, 
            mt: 4, 
            borderRadius: 2,
            textAlign: 'center',
            backgroundColor: theme.palette.background.paper
          }}
        >
          <Typography variant="body2" color="text.secondary">
            Quran data/audio provided by{' '}
            <Link 
              href="https://alquran.cloud" 
              target="_blank" 
              rel="noopener noreferrer"
            >
              AlQuran.cloud
            </Link>
            {' '}and{' '}
            <Link 
              href="https://islamic.network" 
              target="_blank" 
              rel="noopener noreferrer"
            >
              Islamic Network
            </Link>
            . Tajweed rendering powered by iMuslim Life's Tajweed API.
          </Typography>
        </Paper>
      </Container>
    </QuranProgressProvider>
  );
};

// We wrap the QuranPage content with QuranProgressProvider
const QuranPage = () => {
  return (
    <QuranProgressProvider>
      <QuranPageContent />
    </QuranProgressProvider>
  );
};

export default QuranPage;